import { ViewChild, ElementRef, ChangeDetectorRef, ChangeDetectionStrategy } from "@angular/core";
import { fromEvent, Observable, Subject, from } from "rxjs";
import { EnvService } from "src/app/services/env.service";
import { User } from "./../../models/user";
import { Component, OnInit, Input } from "@angular/core";

@Component({
  selector: "app-user-avatar",
  templateUrl: "./user-avatar.component.html",
  styleUrls: ["./user-avatar.component.scss"],
})
export class UserAvatarComponent implements OnInit {
  @Input() user: User;
  @Input() userObservable :Subject<User>;
  hasAvatar: boolean = false;
  @Input() justContainer: boolean = false;
  @ViewChild('avatar', { static:false, read: ElementRef }) avatar: ElementRef;
  img;
  constructor(
    private env: EnvService,
    private changeDetection:ChangeDetectorRef
    ) {}

  ngOnInit() {
    this.img = new Image();
    if(this.userObservable != null)
    {
      
      this.userObservable.subscribe(user=>{
        this.loadImage(user.avatar_thumbnail);
      })
    }
  }

  ngAfterViewInit() {
    if(this.user !=null)
    {
      let avatarUrl  = this.user.avatar_thumbnail;
      if (avatarUrl != null) {
        this.loadImage(avatarUrl);
      }
    }
    
    
  }
  ngOnDestroy() {
    if(this.userObservable != null)
    {
      this.userObservable.unsubscribe();
    }
    
  }
  loadImage(avatarUrl)
  {
    
    this.img.src = this.env.getPublicStorageUrl(avatarUrl);
    return fromEvent(this.img, "load").subscribe((result) => {
      this.avatar.nativeElement.appendChild(this.img);
      this.hasAvatar = true;
      //this.changeDetection.detectChanges()
    });
  }
  
}
