import { ProviderStatus } from './../../models/provider';
import { RemoteStatsType, CategoryStats } from './../../models/remote-stats';
import { EnvService } from 'src/app/services/env.service';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-remote-stats',
  templateUrl: './remote-stats.component.html',
  styleUrls: ['./remote-stats.component.scss'],
})
export class RemoteStatsComponent implements OnInit {
  @Input() statsType:RemoteStatsType;
  @Input() categoryId:number;
  @Output() providerStatusSelected = new EventEmitter();
  isTotalProvidersType = false;
  isCategoryProvidersType = false;
  isUsersTypes = false;
  providerStatus = ProviderStatus;
  selectedPorivderStatus:ProviderStatus = ProviderStatus.NA;
  constructor(
    public env:EnvService
  ) { }

  ngOnInit() {
    this.isTotalProvidersType = this.statsType == RemoteStatsType.TOTAL_PROVIDERS_STATS;
    this.isCategoryProvidersType = this.statsType == RemoteStatsType.CATEGORY_PROVIDERS_STATS;
    this.isUsersTypes = this.statsType == RemoteStatsType.USERS_STATS;
    
  }
  selectProviderStatus(status)
  {
    this.selectedPorivderStatus = status;
    this.providerStatusSelected.emit(status);
  }
  categoryStats() 
  {
    return this.env.remoteStats.category_stats.find(catStat=>{
      return catStat.category_id == this.categoryId;
    });
  }

}
