import { HttpHelperService } from './http-helper.service';
import { EnvService } from 'src/app/services/env.service';
import { Injectable } from '@angular/core';
import { Storage } from "@ionic/storage";
import { SharedResourceAsyncRequest, SharedResourceAsyncRequestBundle } from '../architecture/shared-resource-async-request-bundle';

@Injectable({
  providedIn: 'root'
})
export class SharedResourcesService {

  constructor(
    public env:EnvService,
    public httpHelper:HttpHelperService,
    public storage:Storage
  ) {
    SharedResourceAsyncRequestBundle.HttpHelper = this.httpHelper;
    SharedResourceAsyncRequestBundle.Env = this.env;
    SharedResourceAsyncRequestBundle.Storage = this.storage;
   }
}
