import { LoadingController } from "@ionic/angular";
import { EnvService } from "src/app/services/env.service";
import { AlertService } from "./../../services/alert.service";
import { UserService } from "src/app/services/user.service";
import { concatMap, catchError, take } from "rxjs/operators";
import { ModalController, ActionSheetController } from "@ionic/angular";
import { Component, OnInit, Input } from "@angular/core";
import { of, from } from "rxjs";

@Component({
  selector: "app-deposit",
  templateUrl: "./deposit.component.html",
  styleUrls: ["./deposit.component.scss"],
})
export class DepositComponent implements OnInit {
  @Input() amount = null;
  selectedPreset: number = null;
  depositing = false;
  constructor(
    private modal: ModalController,
    private actionSheet: ActionSheetController,
    private userService: UserService,
    private alert: AlertService,
    private loadingModal: LoadingController,
    private env: EnvService
  ) {}
  submit() {
    let amount = 0;
    if (this.amount) {
      amount = this.amount;
    } else {
      amount = this.selectedPreset;
    }
    if(this.env.MOCK_PAYMENT)
    {
      this.deposit(amount);
      return;
    }
    this.alert.presentLoadingModal(this.loadingModal).pipe(take(1)).subscribe();
    this.userService
      .getPaymentAuthority(amount)
      .pipe(
        catchError((error) => {
          const errorType = error["error"]["error"];
          this.alert.presentError(
            this.env.serverErrorMessage(error.error["error_code"])
          );

          return of(false);
        })
      )
      .subscribe((result) => {
        this.loadingModal.dismiss();
        if (result) {
          window.open(
            "https://www.zarinpal.com/pg/StartPay/" +
              result["authority_code"] +
              "MobileGate"
          );
          this.dismiss();
        }
      });
  }
  openDepostPresets() {
    this.presentDepositPresets().subscribe();
  }
  presentDepositPresets() {
    return from(
      this.actionSheet.create({
        mode: "ios",
        buttons: [
          {
            text: "۱۰۰۰ تومان",
            role: "destructive",
            handler: () => {
              this.selectedPreset = 1000;
            },
          },
          {
            text: "۱۰۰۰۰ تومان",
            role: "destructive",
            handler: () => {
              this.selectedPreset = 10000;
            },
          },
          {
            text: "۲۰۰۰۰ تومان",
            role: "destructive",
            handler: () => {
              this.selectedPreset = 20000;
            },
          },
          { text: "انصراف", role: "cancel" },
        ],
      })
    ).pipe(
      concatMap((modal) => {
        modal.onDidDismiss;
        from(modal.onDidDismiss()).subscribe((data) => {});
        return from(modal.present());
      })
    );
  }
  deposit(amount) {
    this.depositing = true;
    this.userService
      .deposit(amount)
      .pipe(
        catchError((error) => {
          this.alert.presentError("افزایش اعتبار با مشکل روبرو شد.");
          return of(false);
        })
      )
      .subscribe((result) => {
        this.depositing = false;
        if (result) {
          this.alert.presentSuccess("افزایش اعتبار انجام شد.");
          this.userService.updateUserBalance().subscribe();
          this.dismiss();
        }
      });
  }
  ngOnInit() {}
  dismiss(data = null) {
    this.modal.dismiss(data);
  }
}
