import { SingleSharedResource } from "./single-shared-resource";
import { ArrayedSharedResource } from "./arrayed-shared-resource";
import { HttpHelperService } from "./../services/http-helper.service";
import { Observable, of, from } from "rxjs";
import { map } from "rxjs/operators";
import { SharedResourceAsyncRequestBundle } from './shared-resource-async-request-bundle';
export class AutoSingleGetResource<T> extends SingleSharedResource<T, number> {
  constructor(
    private url: string,
    private authenticated: boolean = true
  ) {
    super(null, null, null);
    this.producer = this.producerFunction;
    this.producer = this.producer.bind(this);
  }

  private producerFunction(argument?: number): Observable<T[]> {
    return SharedResourceAsyncRequestBundle.HttpHelper
      .get(this.url + "/" + argument, this.authenticated)
      .pipe(
        map((result) => {
          this.setResource(result as T);
          return result as T[];
        })
      );
  }
}
