import { Payment } from './../models/payment';
import { AutoSingleGetResource } from './../architecture/auto-single-get-resource';
import { HttpHelperService } from './http-helper.service';
import { RemoteStatData } from './../models/remote-stat-data';
import { Discount } from './../models/discount';
import { SharedResourceAsyncRequestBundle } from 'src/app/architecture/shared-resource-async-request-bundle';
import { Invoice } from './../models/invoice';
import { Balance } from './../models/balance';
import { RemoteStats } from './../models/remote-stats';
import { AdditionalInfo } from './../models/additional-info';
import { HttpHeaders } from '@angular/common/http';
import { User, UserStatsType } from './../models/user';

import { from, of } from 'rxjs';
import { AuthService } from 'src/app/services/auth.service';
import { map, concatMap } from 'rxjs/operators';
import { EnvService } from 'src/app/services/env.service';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AutoArrayedGetResource } from '../architecture/auto-arrayed-shared-resource';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  balance:Balance = null;
  balanceShared:SharedResourceAsyncRequestBundle<Balance,null>;
  discountsShared:SharedResourceAsyncRequestBundle<Discount[],null>;
  public statsShared: SharedResourceAsyncRequestBundle<
    RemoteStatData[],
    []
  >;
  public allUsersShared = new AutoArrayedGetResource<User,null>("users/getAll");
  public userShared = new AutoSingleGetResource<User>("users/getById");
  public myPayments = new AutoArrayedGetResource<Payment,true>("users/payments");
  constructor(
    private http:HttpClient,
    private env:EnvService,
    private auth:AuthService,
    private httpHelper:HttpHelperService
  ) {
    this.auth.loggedIn.subscribe(result=>{
      this.balanceShared.request();
    });
    this.auth.loggedOut.subscribe(result=>{
      this.balance = null;
    });
    this.balanceShared = new SharedResourceAsyncRequestBundle<
      Balance,
      null
    >(this.updateUserBalance, null, this);
    this.discountsShared = new SharedResourceAsyncRequestBundle<
      Discount[],
      null
    >(this.getDiscounts, null, this);
    this.statsShared = new SharedResourceAsyncRequestBundle<
      RemoteStatData[],
      []
    >(this.getStats, [], this);
   }
   public updateUserBalance() {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        
        let url = "users/getBalance";
        return this.http
          .get(this.env.API_URL + url, {
            headers: headers,
          })
          .pipe(
            map((result) => {
              this.balanceShared.setResource(result as Balance);
              this.balance = result as Balance;
              return result as Balance;
            })
          );
      })
    );
  } 
  public getById(id: string) {
    
    return from(this.auth.getToken().then(token=>{
      let url = "users/getById/" + id;
      return this.http
        .get(this.env.API_URL + url, {
          headers: {
            Authorization:
                token["token_type"] + " " + token["access_token"],
            Accept: "application/json",
            "Content-Type": "application/json"
          }
        })
        .pipe(map(data => {
          return data as User;
        })).toPromise();
    }));
    
  }
  public getByUid(uid: string) {
    
    return from(this.auth.getToken().then(token=>{
      let url = "users/getByUid/" + uid;
      return this.http
        .get(this.env.API_URL + url, {
          headers: {
            Authorization:
                token["token_type"] + " " + token["access_token"],
            Accept: "application/json",
            "Content-Type": "application/json"
          }
        })
        .pipe(map(data => {
          return data as User;
        })).toPromise();
    }));
    
  }
  public getPaymentAuthority(amount)
  {
    return this.httpHelper.getAuthenticated('payment/getPaymentAuthority/'+amount);
  }
  public updateRemoteStats() {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        
        let url = "stats";
        return this.http
          .get(this.env.API_URL + url, {
            headers: headers,
          })
          .pipe(
            map((result) => {
              this.env.remoteStats = result as RemoteStats;
              return result as RemoteStats;
            })
          );
      })
    );
  }
  public getAdditionalInfo(userId) {
    
    return from(this.auth.getToken()).pipe(concatMap(token=>{
      const headers = new HttpHeaders({
        Authorization:
          token["token_type"] + " " + token["access_token"],
        Accept: "application/json",
        "Content-Type": "application/json"
      });
      let url = "users/getAdditionalInfo/" + userId;
      return this.http
        .get(this.env.API_URL + url, {
          headers: headers
        })
        .pipe(
          map(result => {
            
            return result as AdditionalInfo;
          })
        )
    }));
    
    
  }
  public updateAdditionalInfo(info:AdditionalInfo)
  {
    return from(this.auth.getToken()).pipe(concatMap(token=>{
      const headers = new HttpHeaders({
        Authorization:
          token["token_type"] + " " + token["access_token"],
        Accept: "application/json",
        "Content-Type": "application/json"
      });
      let url = "users/updateInfo";
      return this.http
        .post(this.env.API_URL + url, 
          JSON.stringify(info)
          , {
          headers: headers
        })
        .pipe(
          map(result => {
            return result as AdditionalInfo;
          })
        )
    }));
    
    
  }
  public deposit(amount:number)
  {
    return from(this.auth.getToken()).pipe(concatMap(token=>{
      const headers = new HttpHeaders({
        Authorization:
          token["token_type"] + " " + token["access_token"],
        Accept: "application/json",
        "Content-Type": "application/json"
      });
      let url = "users/deposit";
      return this.http
        .post(this.env.API_URL + url, 
          {
            amount:amount
          }
          , {
          headers: headers
        })
        .pipe(
          map(result => {
            return result as Invoice;
          })
        )
    }));
  }
  private  getDiscounts() {
    
    return from(this.auth.getToken()).pipe(concatMap(token=>{
      const headers = new HttpHeaders({
        Authorization:
          token["token_type"] + " " + token["access_token"],
        Accept: "application/json",
        "Content-Type": "application/json"
      });
      let url = "getDiscounts";
      return this.http
        .get(this.env.API_URL + url, {
          headers: headers
        })
        .pipe(
          map(result => {
            
            this.discountsShared.setResource(result as Discount[]);
            return result as Discount[];
          })
        )
    }));
    
    
  }
  public  useDiscount(discountId:number) {
    
    return from(this.auth.getToken()).pipe(concatMap(token=>{
      const headers = new HttpHeaders({
        Authorization:
          token["token_type"] + " " + token["access_token"],
        Accept: "application/json",
        "Content-Type": "application/json"
      });
      let url = "useDiscount/"+ discountId;
      return this.http
        .post(this.env.API_URL + url, null,{
          headers: headers
        })
        .pipe(
          map(result => {
            
            return result as Invoice;
          })
        )
    }));
    
    
  }
  public getStats() {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        let url = "users/usersStats";
        return this.http
          .get(this.env.API_URL + url, {
            headers: headers,
          })
          .pipe(
            map((result) => {
              result = result as { [key: number]: number };
              let remoteData: RemoteStatData[] = [
                
                {
                  text: 'کل',
                  number: result[UserStatsType.USER_STATS_TOTAL_COUNT],
                  url: null,
                  link_options: null,
                },{
                  text: 'تایید شده',
                  number: result[UserStatsType.USER_STATS_VERIFIED_COUNT],
                  url: null,
                  link_options: null,
                }
              ];
              this.statsShared.setResource(remoteData);
              return remoteData;

            })
          );
      })
    );
  }
}
