import { SessionsManagerService } from 'src/app/services/sessions-manager-service';
import { UserService } from 'src/app/services/user.service';
import { CallService } from './call.service';
import { CallComponent } from "./../common/call/call.component";
import { VoiceCall } from "./../models/voice-call";
import { SessionState } from "src/app/models/session";
import { Session } from "src/app/models/session";
import { from, NotificationKind, Observable } from "rxjs";
import { EnvService } from "src/app/services/env.service";
import { HttpClient } from "@angular/common/http";
import { concatMap, mergeMapTo, take, map, mergeMap } from "rxjs/operators";
import { AlertService } from "src/app/services/alert.service";
import { ActivatedRoute } from "@angular/router";
import { User } from "./../models/user";
import { Platform, NavController } from "@ionic/angular";
import { FirebaseX } from "@ionic-native/firebase-x/ngx";
import { AuthService } from "src/app/services/auth.service";
import { Injectable } from "@angular/core";
import { AngularFireMessaging } from "@angular/fire/messaging";

@Injectable({
  providedIn: "root",
})
export class NotificationService {
  token: string = null;
  showNoitfEnable = false;
  tokenChanges: Observable<string>;
  onMessageReceived: Observable<any>;
  sessionManagerService:SessionsManagerService = null;
  constructor(
    private auth: AuthService,
    private firebaseX: FirebaseX,
    private platform: Platform,
    private route: ActivatedRoute,
    private alert: AlertService,
    private http: HttpClient,
    private env: EnvService,
    private nav: NavController,
    private afMessaging: AngularFireMessaging,
    private callService:CallService,
    private userService:UserService
  ) {
    
    if (!this.env.WEB_NOTIFICATIONS_ONLY && this.platform.is("android")) {
      ///if(false){
      this.platform.ready().then((ready) => {
        this.setAndroidParameters();
        this.subscribeToEvents();
      });
    } else {
      this.setBrowserParameters();
      this.subscribeToEvents();
    }
    
  }
  private subscribeToEvents()
  {
    this.onMessageReceived.subscribe((message) => {
      console.log("message received: " + message);
      this.dispatchNotification(message);
    });
    this.tokenChanges.subscribe((token) => {
      console.log("token refresh: " + token);
      this.token = token;
      this.auth
        .isAuthenticated()
        .then((result) => {
          this.setToken();
        })
        .catch((err) => {});
    });
    
    this.auth
      .isAuthenticated()
      .then((result) => {
        this.setToken();
      })
      .catch((err) => {});
    this.auth.roleDetected.subscribe((result) => {
      if (result) {
        console.log('before setToken');
        this.setToken();
      }
    });
    this.auth.loggedOutExToken.subscribe(exToken=>{
      if(exToken)
      {
        this.unsetToken(exToken);
      }
    });
  }
  private handleNewMessage(data) {
    let user = JSON.parse(data.sender) as User;
    let notifMessage = "پیام جدید از ";
    notifMessage += User.getName(user);
    let belongsToSessionChat = (data.topic as string).substr(0, 3) == "grp";
    if (belongsToSessionChat) {
      //this.alert.presentViewNotify(notifMessage, this.openSessionsPage, this);
    } else {
      this.alert.presentViewNotify(notifMessage, this.openPeerChatList, this);
    }
  }
  public setAndroidParameters() {
    
    this.onMessageReceived = this.firebaseX.onMessageReceived();
    this.tokenChanges = this.firebaseX.onTokenRefresh();
  }
  public setBrowserParameters() {
    this.onMessageReceived = this.afMessaging.messages.pipe(
      map((message) => message["data"])
    );
    this.tokenChanges = this.afMessaging.requestPermission.pipe(
      mergeMapTo(this.afMessaging.tokenChanges)
    );
    
  }

  private dispatchNotification(data) {
    let notificationCode = parseInt(data["notification_code"]);
    switch (notificationCode) {
      case NotificationCode.NEW_MESSAGE:
        this.handleNewMessage(data);
        break;
      case NotificationCode.INCOMING_CALL:
        this.handleIncomingCall(data);
        break;
      case NotificationCode.SESSION_UPDATE:
        this.handleSessionUpdateNotif(data);
        break;
      case NotificationCode.PAYMENT_CONFIRMED:
        this.handlePaymentConfirmed();
        break;
      default:
    }
  }
  private handlePaymentConfirmed()
  {
    this.userService.balanceShared.request();
    this.alert.presentSuccess("افزایش اعتبار انجام شد.");
  }
  private handleIncomingCall(data) {
    let call = new VoiceCall();
    call.id = data["call_id"];
    call.access_token = data["access_token"];
    call.maxDuration = data['max_duration'];
    call.caller = JSON.parse( data["caller"]) as User;
    this.callService.receiveCall(call);
  }
  private handleSessionUpdateNotif(message) {
    if(this.sessionManagerService)
    {
      let session = JSON.parse(message.session) as Session;
      let sender = JSON.parse(message.sender);
      this.sessionManagerService.handleIncomingSessionUpdateNotification(session, sender)
    }
    
    
  }

  openPeerChatList() {
    this.auth.getUser().then((user) => {
      this.nav.navigateRoot(["/peer-chats/"+user.role_id]);
     
    });
  }
  
  setToken() {
    if (this.token) {
      this.auth.remoteSetFcmToken(this.token).subscribe();
    }
  }
  unsetToken(exAuthToken) {
    if(this.platform.is("android"))
    {
      this.firebaseX.unregister();
    }
    else
    {
      this.afMessaging.getToken
      .pipe(mergeMap(token => this.afMessaging.deleteToken(token)))
    }

    
      this.auth.remoteUnsetFcmToken(exAuthToken).subscribe();
    
  }

  notifyPvdSessionAccepted(sessionId: number) {}
  notifySessionUpdate(sessionId: number, recipientId: number) {
    let url = "notify/sessionUpdate";
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        return this.http.post(
          this.env.API_URL + url,
          {
            recipient_user_id: recipientId,
            session_id: sessionId,
          },
          {
            headers: {
              Authorization: token["token_type"] + " " + token["access_token"],
              Accept: "application/json",
              "Content-Type": "application/json",
            },
          }
        );
      })
    );
  }
  notifySentMessage(recipientId: number, topic: string) {
    let url = "notify/sentMessage";
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        return this.http.post(
          this.env.API_URL + url,
          {
            recipient_user_id: recipientId,
            topic: topic,
          },
          {
            headers: {
              Authorization: token["token_type"] + " " + token["access_token"],
              Accept: "application/json",
              "Content-Type": "application/json",
            },
          }
        );
      })
    );
  }
}
