import { IMService } from 'src/app/services/im.service';
import { Subscription } from "rxjs";
import { EnvService } from "src/app/services/env.service";
import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-loading-toolbar-button",
  templateUrl: "./loading-toolbar-button.component.html",
  styleUrls: ["./loading-toolbar-button.component.scss"],
})
export class LoadingToolbarButtonComponent implements OnInit {
  constructor(private env: EnvService, private imService:IMService) {}
  visible = false;
  imNoConnection = false;
  pendingSubscription: Subscription;
  imConnectionSubscription:Subscription;
  imRetryRemainingSeconds = 0;
  imAutoReconnectCountdownSubscription : Subscription;
  ngOnInit() {
    this.visible == this.env.somethingPendingCount > 0;
    this.pendingSubscription = this.env.somethingPendingUpdated.subscribe(
      (pending) => {
        this.visible = pending;
      }
    );
    this.imConnectionSubscription = this.imService.connectionStatusChanged.subscribe(result=>{
      this.imNoConnection = !result;
    });
    this.imAutoReconnectCountdownSubscription = this.imService.autoReconnectRemainingSecondsUpdated.subscribe(result=>{
      this.imRetryRemainingSeconds = result;
    })
  }
  ionViewWillEnter() {
    
  }
  ngOnDestroy(): void {
    this.pendingSubscription.unsubscribe();
    this.imConnectionSubscription.unsubscribe();
    this.imAutoReconnectCountdownSubscription.unsubscribe();
  }
  ionViewWillLeave() {
    
  }
  imReconnect()
  {
    this.imService.reconnect();
  }
}
