import { Provider } from './../../models/provider';
import { SessionsManagerService } from './../../services/sessions-manager-service';
import { EnvService } from 'src/app/services/env.service';
import { User } from './../../models/user';
import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-basic-user-profile',
  templateUrl: './basic-user-profile.component.html',
  styleUrls: ['./basic-user-profile.component.scss'],
})
export class BasicUserProfileComponent implements OnInit {
  @Input() user:User;
  @Input() provider:Provider = null;
  getName = User.getName;

  constructor(
    public env:EnvService,
    private sessionsManagerService:SessionsManagerService
  ) { }

  ngOnInit() {}
  isOnline() {
    return this.sessionsManagerService.isUserOnline(this.user);
  }

}
