import { SessionsManagerService } from 'src/app/services/sessions-manager-service';
import { AlertService } from 'src/app/services/alert.service';
import { Session } from 'src/app/models/session';
import { ModalController } from '@ionic/angular';
import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-score',
  templateUrl: './score.component.html',
  styleUrls: ['./score.component.scss'],
})
export class ScoreComponent implements OnInit {
  @Input() score:number=null;
  @Input() session:Session;
  constructor(
    private modalController:ModalController,
    private alert:AlertService,
    private sessionsManager:SessionsManagerService
  ) { }

  ngOnInit() {}
  dismiss()
  {
    this.modalController.dismiss();
  }
  setScore(score)
  {
    this.score = score;
  }
  submit()
  {
    this.sessionsManager.updateScore(this.session.id, this.score).subscribe(result=>{
      this.alert.presentSuccess("امتیاز شما ثبت شد.");
      this.modalController.dismiss(this.score);
    })
    
    

  }
}
