import { from, Observable, Subject } from "rxjs";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Platform } from "@ionic/angular";
import { map, first } from "rxjs/operators";
import { AndroidPermissions } from "@ionic-native/android-permissions/ngx";

declare var MediaRecorder: any;

@Injectable({
  providedIn: "root",
})
export class SmartAudioService {
  audioType: string = "html5";
  sounds: any = [];
  mediaRecorder;
  recordingStarted = new Subject<void>();
  recordingStopped = new Subject<void>();
  constructor(
    private platform: Platform,
    private http: HttpClient,
    private androidPermissions: AndroidPermissions
  ) {
    if (platform.is("cordova")) {
      this.audioType = "native";
    }
    this.draw = this.draw.bind(this);
  }
  analyser: AnalyserNode;
  dataArray;
  bufferLength;
  sourcePlaying = false;

  draw() {
    if (this.sourcePlaying) {
      let drawVisual = requestAnimationFrame(this.draw);
    }

    this.analyser.getByteFrequencyData(this.dataArray);
    var barWidth = (100 / this.bufferLength) * 2.5;
    var x = 0;
    var barHeight;
    for (var i = 0; i < this.bufferLength; i++) {
      barHeight = this.dataArray[i] / 2;

      x += barWidth + 1;
    }
  }
  preload(key, asset) {
    let audio = {
      key: key,
      asset: asset,
      type: "html5",
    };

    this.sounds.push(audio);
  }

  play(key, loop=false) {
    return new Observable<HTMLAudioElement>((observer) => {
      let audio = this.sounds.find((sound) => {
        return sound.key === key;
      });

      let audioAsset = new Audio(audio.asset);
      audioAsset.addEventListener("ended", () => {
        //observer.next(true);
        observer.complete();
      });
      if(loop)
      {
        audioAsset.loop = true;
      }
      audioAsset.play().then(()=>
        {
          observer.next(audioAsset);
          
        }
      );
    });
  }
  
  pause(auidoHandle:HTMLAudioElement)
  {
    auidoHandle.pause();
  }

  stopRecord() {
    if (this.mediaRecorder) {
      this.mediaRecorder.stop();
    }
  }
  checkPermission() {
    if (this.platform.is("cordova")) {
      return this.androidPermissions
        .checkPermission(this.androidPermissions.PERMISSION.RECORD_AUDIO)
        .then((result) => {
          if (result.hasPermission) {
            return true;
          } else {
            return false;
          }
        });
    }
    return Promise.resolve(true);
  }
  askPermission() {
    return this.androidPermissions
      .requestPermission(this.androidPermissions.PERMISSION.RECORD_AUDIO)
      .then((result) => {
        if (result.hasPermission) {
          return true;
        }
        return false;
      });
  }
  startRecord() {
    return new Observable<Blob>((observer) => {
      from(navigator.mediaDevices.getUserMedia({ audio: true }))
        .pipe(first())
        .subscribe((stream) => {
          this.mediaRecorder = new MediaRecorder(stream);
          console.log("media recorder");
          //this.androidPermissions.requestPermissions([this.androidPermissions.PERMISSION.RECORD]);
          this.play("beep1").subscribe((result) => {
            this.mediaRecorder.start();
          });

          const audioChunks = [];
          this.mediaRecorder.addEventListener("dataavailable", (event) => {
            audioChunks.push(event.data);
          });
          this.mediaRecorder.addEventListener("start", () => {
            this.recordingStarted.next();
          });
          this.mediaRecorder.addEventListener("stop", () => {
            const audioBlob = new Blob(audioChunks);
            observer.next(audioBlob);
            this.recordingStopped.next();
            observer.complete();
            //const audioUrl = URL.createObjectURL(audioBlob);
            //const audio = new Audio(audioUrl);
            //audio.play();
          });
        });
    });
  }
}
