import { AlertService } from 'src/app/services/alert.service';
import { PublicQuestionAndAnswersService } from './../../services/public-question-and-answers.service';
import { ModalController } from '@ionic/angular';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-ask-question',
  templateUrl: './ask-question.component.html',
  styleUrls: ['./ask-question.component.scss'],
})
export class AskQuestionComponent implements OnInit {
  aksing:boolean = false;
  questionContent:string ="";
  constructor(
    private modalController:ModalController,
    private qaService: PublicQuestionAndAnswersService,
    private alert:AlertService
  ) { }

  ngOnInit() {}
  dismiss()
  {
    this.modalController.dismiss();
  }
  ask()
  {
    if(this.questionContent.length > 20)
    {
      this.qaService.aks(this.questionContent).subscribe(result=>{
        this.alert.presentSuccess("سوال با موفقیت اضافه شد");
        this.qaService.questionsShared.request();
        this.dismiss();
      })
    }
    
  }
}
