import { SharedResourceAsyncRequestBundle } from 'src/app/architecture/shared-resource-async-request-bundle';
import { ArrayedSharedResource } from './arrayed-shared-resource';
import { HttpHelperService } from "./../services/http-helper.service";
import { Observable, of, from } from "rxjs";
import { map } from "rxjs/operators";
export class AutoArrayedGetResource<
  T,
  Z
> extends ArrayedSharedResource<T, Z> {
  
  constructor(private url: string,  private authenticated:boolean=true) {
    super(null, [], null);
    this.producer = this.producerFunction;
    this.producer = this.producer.bind(this);
  }

  private producerFunction(argument?: Z): Observable<T[]> {
    let url = this.url;
    if(argument)
    {
      url = url+"/"+argument;
    }
    return SharedResourceAsyncRequestBundle.HttpHelper.get(url, this.authenticated).pipe(
      map((result) => {
        this.setResource(result as T[]);
        return result as T[];
      })
    );
  }
}
