import { FileModel } from "./file-model";
import { ProviderVerificationDocument } from "./provider-verification-document";
import { Observable } from "rxjs";
import { Model } from "./model";
import { ProviderGroup } from "./provider-group";
import { User } from "./user";
export class Provider extends Model {
  id: number;
  provider_category_id: number;
  user_id: number;
  user: User;
  provider_categories: ProviderGroup[];
  verified_by_admin: boolean;
  provider_verification_documents: ProviderVerificationDocument[];
  certificateFile: FileModel;
  per_minute_text_fee;
  per_minute_call_fee;
  status: ProviderStatus;
  education_degree: EducationalDegree;
  about_me: string;
  ended_sessions_count: number;
  mean_score: number;
  public static getVerifiedByAdminText(provider: Provider) {
    if (provider.verified_by_admin) {
      return "تأیید شده";
    }
    return "در انتظار تأیید";
  }
  public static getStatusText(status: ProviderStatus) {
    switch (status) {
      case ProviderStatus.OFFLINE:
        return "غیرفعال";
      case ProviderStatus.IN_SESSION:
        return "در جلسه";
      case ProviderStatus.ONLINE:
        return "فعال";
    }
  }
  public static getStatusClass(status: ProviderStatus) {
    switch (status) {
      case ProviderStatus.OFFLINE:
        return "offline";
      case ProviderStatus.IN_SESSION:
        return "in-session";
      case ProviderStatus.ONLINE:
        return "online";
    }
  }
  public static getCertificateFileName(provider: Provider) {
    if(provider.provider_verification_documents == null)
    {
      return false;
    }
    let doc = provider.provider_verification_documents.find((document) => {
      return document.title == "certificate";
    });

    if (doc) {
      return doc.url;
    }
    return false;
  }
  public static getDegreeString(degree: EducationalDegree) {
    switch (degree) {
      case EducationalDegree.HIGH_SCHOOL_DIPLOMA:
        return "دیپلم";
      case EducationalDegree.BACHELORS:
        return "کارشناسی";
      case EducationalDegree.MASTERS:
        return "کارشناسی ارشد";
      case EducationalDegree.PHD:
        return "دکتری";
    }
  }
  public static getStatTypeString(statType: ProviderStatsType) {
    switch (statType) {
      case ProviderStatsType.PROVIDER_STATS_ONLINE_COUNT:
        return "آنلاین";
      case ProviderStatsType.PROVIDER_STATS_IN_SESSION_COUNT:
        return "در حال مشاوره";
      case ProviderStatsType.PROVIDER_STATS_TOTAL_COUNT:
        return "کل";
    }
  }
}

export enum ProviderStatus {
  OFFLINE = 0,
  ONLINE = 1,
  IN_SESSION = 2,
  NA = 3,
}
export enum ProviderStatsType {
  PROVIDER_STATS_ONLINE_COUNT = 1,
  PROVIDER_STATS_IN_SESSION_COUNT = 2,
  PROVIDER_STATS_TOTAL_COUNT = 3,
}
export enum EducationalDegree {
  HIGH_SCHOOL_DIPLOMA = 1,
  BACHELORS = 2,
  MASTERS = 3,
  PHD = 4,
}
