import { Observable } from 'rxjs';
export class Model {

    mustFetchItself:boolean= false;
    isFetching:boolean=false;
    fetchParameter: number|string = null;
    fetchCallback : ((findParameter:number | string) => Observable<any>) = null;
    fetchObject:any = null;
    constructor(mustFetchItself?:boolean
    , fetchCallback?:((findParameter:number | string) => Observable<any>)
    , fetchObject?:any
    , fetchParameter?:number | string )
    {
        this.mustFetchItself = mustFetchItself || false;
        
        
        this.fetchParameter = fetchParameter || null;
        this.fetchObject = fetchObject || null;
        if(this.mustFetchItself)
        {
            this.fetchCallback = fetchCallback.bind(fetchObject) || null;
            this.isFetching = false;
            this.fetch();
        }

    }
    fetch()
    {
        if(!this.isFetching)
        {
            this.isFetching = true;
            this.fetchCallback(this.fetchParameter).subscribe(result=>{
                Object.assign(this, result);
                this.isFetching = false;
            })
        }
        
    }
    
    
}
