  private getUsrPeerProviders() {
    let uids = [];
    this.imService.chatList
      .filter((contact) => {
        return (contact.topic as string).substr(0, 3) == "usr";
      })
      .forEach((contact) => {
        uids.push(contact.topic);

        if (
          this.usrPeerProviders.find((provider) => {
            return provider.user.tinode_uid == contact.topic;
          }) == undefined
        ) {
          let provider = new Provider(
            true,
            this.providerService.getByUid,
            this.providerService,
            contact.topic
          );
          provider.user = new User();
          provider.user.tinode_uid = contact.topic;
          this.usrPeerProviders.push(provider);
        }
      });
  }
  private getPvdPeerUsers() {
    let uids = [];
    this.imService.chatList
      .filter((contact) => {
        return (contact.topic as string).substr(0, 3) == "usr";
      })
      .forEach((contact) => {
        uids.push(contact.topic);

        if (
          this.pvdPeerUsers.find((user) => {
            return user.tinode_uid == contact.topic;
          }) == undefined
        ) {
          let user = new User(
            true,
            this.userService.getByUid,
            this.userService,
            contact.topic
          );

          user.tinode_uid = contact.topic;
          this.pvdPeerUsers.push(user);
        }
      });
  }
