import { Platform } from '@ionic/angular';
import { FileModel } from './../models/file-model';
import { AuthService } from 'src/app/services/auth.service';
import { EnvService } from 'src/app/services/env.service';
import { HttpClient, HttpEventType, HttpEvent, HttpProgressEvent, HttpResponse } from '@angular/common/http';
import { tap, take, first, concatMap, catchError, last, mergeMap } from "rxjs/operators";
import { Observable, concat, of, race, throwError } from "rxjs";
import { map } from "rxjs/operators";

import { Injectable } from "@angular/core";
import { fromEvent, from } from "rxjs";
import { AndroidPermissions } from '@ionic-native/android-permissions/ngx';

@Injectable({
  providedIn: "root"
})
export class FileHelperService {
  constructor(private http:HttpClient,
    private env:EnvService,
    private androidPermissions: AndroidPermissions,
    private platform: Platform,
    private auth:AuthService) {}

  imageMimeTypes = ["image/jpeg","image/gif", "image/png", "image/svg", "image/svg+xml"];
  audioMimeTypes = ["audio/webm"];
  uploadImage( image:FileModel) {
    return this.uploadToServer(image);
  }
  uploadUserAvatar(image:FileModel)
  {
    return this.uploadAvatarToServer(image);
  }
  public processImageProperties(image:FileModel)
  {
    image.name = this.getFileName(image.content);

    return this.getMimeType(image, this.imageMimeTypes).pipe(concatMap(result=>{
      image.mime_type = result;
       image.img_src= URL.createObjectURL(image.content);
      return this.getImageDimensions(image).pipe(
      first());
    }),
      first()
    );
  }
  downloadImage(image:FileModel)
  {
    return this.downloadFromServer(image,'files/downloadMessageFile/');
  }
  downloadAudio(audio:FileModel)
  {
    return this.downloadFromServer(audio,'files/downloadMessageFile/');
  }
  downloadVerificationDocument(file:FileModel)
  {
    return this.downloadFromServer(file,'providers/downloadVerificationDocument/', 'image/jpeg');
  }
  private getFileName(file): string {
    return file.name;
  }
  public uploadAudio(audio:FileModel)
  {
    return this.uploadToServer(audio);
  }
  public getImageDimensions(image:FileModel)
  {
    var img = new Image();
    img.crossOrigin = "Anonymous";
   
    img.src = image.img_src;
    return fromEvent(img, "load").pipe(map(event=>
    {
      let e = event.target as HTMLImageElement;
      image.height = e.height;
      image.width = e.width;
      return image;
    }));
  }
  private getMimeType(file:FileModel, allowedMimeTypes): Observable<string> {
    
    let observable = new Observable<string>(observer=>{
      var reader = new FileReader();
      reader.onload = (event)=>{
        var parts = ((event.target as FileReader).result as string).split(",");
        var mimeTemp = /^data:(image\/[-+a-z0-9.]+);base64/.exec(parts[0]);
        var mime = null;
        if (mimeTemp && mimeTemp.length > 1) {
          mime = mimeTemp[1];
        }
        if (allowedMimeTypes.includes(mime)) {
            
          observer.next(mime);
          observer.complete();
        } else {
          observer.error("error");
        }
      };
      reader.readAsDataURL(file.content);
    })
    return observable;
    
    
  }
  uploadToServer(file:FileModel)
  {
    let url = "files/uploadMessageFile";
    return from(this.auth.getToken()).pipe(concatMap(token => {
      let formData = new FormData();
      formData.append("file", file.content, file.name)
      
      return this.http
        .post(this.env.API_URL + url,formData, {
          headers: {
            Authorization: token["token_type"] + " " + token["access_token"],
            Accept: "application/json",
          },
          reportProgress : true,
          observe:'events'
        })
        .pipe(
          map(result => {
            return this.getEventMessage(result, file)
          }),
          last(),
          map(result=>{
            return (result as HttpResponse<FileModel>).body as FileModel
          })
          
          
        )
    }),first());
  }
  uploadVerifciationDocument(file:FileModel, title:string )
  {
    let url = "providers/uploadVerificationDocument";
    return from(this.auth.getToken()).pipe(concatMap(token => {
      let formData = new FormData();
      formData.append("file", file.content, file.name)
      formData.append('title', title);
      return this.http
        .post(this.env.API_URL + url,formData, {
          headers: {
            Authorization: token["token_type"] + " " + token["access_token"],
            Accept: "application/json",
          },
          reportProgress : true,
          observe:'events'
        })
        .pipe(
          map(result => {
            return this.getEventMessage(result, file)
          }),
          last(),
          map(result=>{
            return (result as HttpResponse<FileModel>).body as FileModel
          })
          
          
        )
    }),first());
  }
  uploadAvatarToServer(file:FileModel)
  {
    let url = "files/uploadAvatar";
    return from(this.auth.getToken()).pipe(concatMap(token => {
      let formData = new FormData();
      formData.append("file", file.content, file.name)
      
      return this.http
        .post(this.env.API_URL + url,formData, {
          headers: {
            Authorization: token["token_type"] + " " + token["access_token"],
            Accept: "application/json",
          },
          reportProgress : true,
          observe:'events'
        })
        .pipe(
          map(result => {
            return this.getEventMessage(result, file)
          }),
          last(),
          map(result=>{
            return (result as HttpResponse<FileModel>).body as FileModel
          })
          
          
        )
    }),first());
  }
  isImageMime(mime)
  {
    return this.imageMimeTypes.includes(mime);
  }
  isAudioMime(mime)
  {
    return this.audioMimeTypes.includes(mime);
  }
  
  downloadFromServer(file:FileModel, url, mime= null)
  {
    let mimeType = mime;
    if(mimeType == null)
    {
      mimeType = file.mime_type;
    }
    url = url+file.name;
    return from(this.auth.getToken()).pipe(concatMap(token => {
      return this.http
        .get(this.env.API_URL + url, {
          headers: {
            Authorization: token["token_type"] + " " + token["access_token"],
            Accept: mimeType,
            "Content-Type" : mimeType
          },
          reportProgress : true,
          observe:'events',
          responseType:"blob"
        })
        .pipe(
          map(result => {
            return this.getEventMessage(result, file)
          }),
          last(),
          map(result=>{
            return (result as HttpResponse<FileModel>).body as FileModel
          })
        )
    }),first());
  }
  private getEventMessage(event: HttpEvent<any>, file: FileModel) {
    if(event.type === HttpEventType.UploadProgress)
    {
      const percentDone = Math.round(100 * (event as HttpProgressEvent).loaded / (event as HttpProgressEvent).total);
      file.upload_progress = percentDone;
      file.emmitFileProgressHappend();
    }
    if(event.type === HttpEventType.DownloadProgress)
    {
      const percentDone = Math.round(100 * (event as HttpProgressEvent).loaded / (event as HttpProgressEvent).total);
      file.fetch_progress = percentDone;
      file.emmitFileProgressHappend();
    }
    return event;
    
  }
  checkPermission() {
    if (this.platform.is("cordova")) {
      return this.androidPermissions
        .checkPermission(this.androidPermissions.PERMISSION.READ_EXTERNAL_STORAGE)
        .then((result) => {
          if (result.hasPermission) {
            return true;
          } else {
            return false;
          }
        });
    }
    return Promise.resolve(true);
  }
  askPermission() {
    return this.androidPermissions
      .requestPermission(this.androidPermissions.PERMISSION.READ_EXTERNAL_STORAGE)
      .then((result) => {
        if (result.hasPermission) {
          return true;
        }
        return false;
      });
  }
}
