import { SharedResourceUpdateStatus } from './../architecture/shared-resource-async-request-bundle';
import { AutoSingleGetResource } from "./../architecture/auto-single-get-resource";
import { HttpHelperService } from "./http-helper.service";
import { AutoArrayedGetResource } from "src/app/architecture/auto-arrayed-shared-resource";
import { RemoteStatData } from "./../models/remote-stat-data";
import { SharedResourceAsyncRequestBundle } from "src/app/architecture/shared-resource-async-request-bundle";
import { Provider, ProviderStatsType } from "src/app/models/provider";
import { FileModel, FileFetchStatus } from "./../models/file-model";
import { FileHelperService } from "./file-helper.service";
import { AuthService } from "src/app/services/auth.service";
import { from, of, Subject } from "rxjs";
import { tap, mergeMap, concatMap, first } from "rxjs/operators";
import { FavoriteProvider } from "./../models/favorite-provider";
import { ProviderGroup } from "./../models/provider-group";
import { EnvService } from "./env.service";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { map } from "rxjs/operators";
import { Observable } from "rxjs";
import { AdditionalInfo } from "../models/additional-info";

@Injectable({
  providedIn: "root",
})
export class ProviderService {
  private selectedGroup: ProviderGroup;
  private selectedProvider: Provider;
  public selectedProviderShared = new AutoSingleGetResource<Provider>(
    "providers/getById"
  );
  public providerByUserIdShared = new AutoSingleGetResource<Provider>(
    "providers/getByUserId"
  );
  public randomAvatarSrcListShared = new AutoArrayedGetResource<string, null>(
    "providers/getRandomAvatars",
    false
  );

  public providerCategoriesShared = new AutoArrayedGetResource<
    ProviderGroup,
    null
  >("categories", false);
  public providerCategoryShared = new AutoSingleGetResource<ProviderGroup>(
    "categories",
    false
  );
  public statsByStatusShared: SharedResourceAsyncRequestBundle<
    RemoteStatData[],
    []
  >;

  public providersByCategoryIdShared = new AutoArrayedGetResource<
    Provider,
    number
  >("providers/getByCategoryId", false);
  public allProvidersShared: AutoArrayedGetResource<Provider, null>;
  providerGroups: ProviderGroup[];
  public favoriteProvidersShared = new AutoArrayedGetResource<
    Provider,
    null
  >("providers/favorites", true);
  favoriteProviders: FavoriteProvider[] = [];
  fetchedFavorites = false;
  providerCategoriesUpdated: Subject<ProviderGroup[]> = new Subject();

  constructor(
    private http: HttpClient,
    private env: EnvService,
    private auth: AuthService,
    private fileHelper: FileHelperService,
    private httpHelper: HttpHelperService
  ) {
    this.auth.loggedOut.subscribe((result) => {
      if (result == true) {
        this.favoriteProviders = [];
        this.fetchedFavorites = false;
      }
    });

    this.allProvidersShared = new AutoArrayedGetResource<Provider, null>(
      "providers/getAll"
    );

    this.statsByStatusShared = new SharedResourceAsyncRequestBundle<
      RemoteStatData[],
      []
    >(this.statsByStatus, [], this);
  }
  getProviderByUserId(userId) {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        let url = "providers/getByUserId/" + userId;
        return this.http
          .get(this.env.API_URL + url, {
            headers: headers,
          })
          .pipe(
            map((result) => {
              return result as Provider;
            })
          );
      })
    );
  }
  private getProviders() {
    let url = "providers/getAll";
    return this.http
      .get(this.env.API_URL + url, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      })
      .pipe(
        map((data) => {
          this.allProvidersShared.setResource(data as Provider[]);
          return data as Provider[];
        })
      );
  }

  public verifyProvider(provider: Provider) {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        let url = "providers/verify/" + provider.id;
        return this.http.post(this.env.API_URL + url, null, {
          headers: headers,
        });
      })
    );
  }
  public addCategory(category: ProviderGroup) {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        let url = "providers/addCategory";
        return this.http
          .post(
            this.env.API_URL + url,
            {
              name: category.name,
              description: category.description,
              icon_name: category.icon_name,
            },
            {
              headers: headers,
            }
          )
          .pipe(
            map((categoryResult) => {
              this.providerGroups.push(categoryResult as ProviderGroup);
              this.providerCategoriesUpdated.next(this.providerGroups);
              return categoryResult as ProviderGroup;
            })
          );
      })
    );
  }

  public editCategory(category: ProviderGroup) {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        let url = "providers/editCategory";
        return this.http
          .post(
            this.env.API_URL + url,
            {
              id: category.id,
              name: category.name,
              description: category.description,
              icon_name: category.icon_name,
            },
            {
              headers: headers,
            }
          )
          .pipe(
            map((categoryResult) => {
              this.updateCategoryInProviderGroups(
                categoryResult as ProviderGroup
              );
              return categoryResult as ProviderGroup;
            })
          );
      })
    );
  }
  public getFees() {
    let url = "providers/getFees";
    return this.http
      .get(this.env.API_URL + url, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      })
      .pipe(map((data) => data));
  }
  updateCategoryInProviderGroups(category: ProviderGroup) {
    let oldCat = this.providerGroups.find((cat) => {
      return cat.id == category.id;
    });
    oldCat.name = category.name;
    oldCat.icon_name = category.icon_name;

    this.providerCategoriesUpdated.next(this.providerGroups);
  }

  public getByUid(uid: string) {
    let url = "providers/getByUid/" + uid;
    return this.http
      .get(this.env.API_URL + url, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      })
      .pipe(map((data) => data));
  }
  public getById(id: number): Observable<Provider> {
    let url = "providers/getById/" + id;
    return this.http
      .get<Provider>(this.env.API_URL + url, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      })
      .pipe(
        map((data) => {
          this.selectedProviderShared.setResource(data as Provider);
          return data;
        })
      );
  }

  public getSelectedGroup() {
    return this.selectedGroup;
  }
  public setSelectedProvider(provider: Provider) {
    this.selectedProvider = provider;
  }
  public getSelectedProvider(): Provider {
    return this.selectedProvider;
  }
  private getRemoteFavorites() {
    return this.auth.getToken().then((token) => {
      let url = "providers/favorites";
      return this.http
        .get(this.env.API_URL + url, {
          headers: {
            Authorization: token["token_type"] + " " + token["access_token"],
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        })
        .pipe(
          map((result) => {
            this.fetchedFavorites = true;
            this.favoriteProviders = result as FavoriteProvider[];
            return this.favoriteProviders;
          })
        )
        .toPromise();
    });
  }
  public getFavoriteProviders(): Observable<FavoriteProvider[]> {
    if (!this.fetchedFavorites) {
      return from(this.getRemoteFavorites());
    }
    return of(this.favoriteProviders);
  }
  public addFavorite(providerId) {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        let url = "providers/addFavorite/" + providerId;
        return this.http
          .post<FavoriteProvider>(this.env.API_URL + url, null, {
            headers: headers,
          })
          .pipe(
            map((favoriteProvider) => {
              this.favoriteProviders.push(favoriteProvider);
              return true;
            })
          );
      })
    );
  }

  public toggleActivitySwitch(state: boolean) {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        let functionName = "activitySwitchOn";
        if (!state) {
          functionName = "activitySwitchOff";
        }
        let url = "providers/" + functionName;
        return this.http
          .post<FavoriteProvider>(this.env.API_URL + url, null, {
            headers: headers,
          })
          .pipe(
            map((favoriteProvider) => {
              return true;
            })
          );
      })
    );
  }
  public getActivitySwitch() {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });

        let url = "providers/getActivitySwitch";
        return this.http
          .get(this.env.API_URL + url, {
            headers: headers,
          })
          .pipe(
            map((result) => {
              return result as boolean;
            })
          );
      })
    );
  }

  public deleteFavorite(providerId) {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        let url = "providers/deleteFavorite/" + providerId;
        return this.http
          .post<FavoriteProvider>(this.env.API_URL + url, null, {
            headers: headers,
          })
          .pipe(
            map((favoriteProvider) => {
              let provider = this.favoriteProviders.find((pvd) => {
                return pvd.provider_id == providerId;
              });
              this.favoriteProviders.splice(
                this.favoriteProviders.indexOf(provider as FavoriteProvider),
                1
              );
              return true;
            })
          );
      })
    );
  }
  public isFavorite(providerId): Observable<Provider | boolean> {
    if(this.favoriteProvidersShared.getResourceStatus() == SharedResourceUpdateStatus.NOT_UPDATED)
    {
      this.favoriteProvidersShared.request();
    }
    if(this.favoriteProvidersShared.resource().length < 1)
    {
      return of(false);
    }
    let contact = this.favoriteProvidersShared.resource().filter(
      (favorite) => favorite.id == providerId
    );
    if (contact.length) {
      return of(contact[0]);
    }
    return of(false);
  }
  public toggleFavorite(providerId) {
    return this.isFavorite(providerId).pipe(
      concatMap((result) => {
        if (result) {
          return this.deleteFavorite(providerId);
        } else {
          return this.addFavorite(providerId);
        }
      })
    );
  }
  uploadCertificate(file: FileModel) {
    return this.fileHelper.uploadVerifciationDocument(file, "certificate");
  }
  downloadCertificate(provider: Provider) {
    provider.certificateFile.fetch_status =
      FileFetchStatus.FILE_FETCH_STATUS_FETCHING;
    return this.fileHelper
      .downloadVerificationDocument(provider.certificateFile)
      .pipe(
        concatMap((result) => {
          provider.certificateFile.content = result;
          provider.certificateFile.img_src = URL.createObjectURL(
            provider.certificateFile.content
          );
          return this.fileHelper
            .getImageDimensions(provider.certificateFile)
            .pipe(
              map((output) => {
                provider.certificateFile = output;
                provider.certificateFile.fetch_status =
                  FileFetchStatus.FILE_FETCH_STATUS_SUCCESS;

                return provider.certificateFile;
              }),
              first()
            );
        })
      );
  }
  favoriteClass(providerId) {
    return this.isFavorite(providerId).pipe(
      map((result) => (result != false ? "favorite" : ""))
    );
  }
  public updateProviderInfo(provider: Provider) {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        let url = "providers/updateProviderInfo/" + provider.id;
        return this.http
          .post(
            this.env.API_URL + url,
            {
              provider_categories: provider.provider_categories.map(
                (cat) => cat.id
              ),
              per_minute_text_fee: provider.per_minute_text_fee,
              per_minute_call_fee: provider.per_minute_call_fee,
              education_degree: provider.education_degree,
            },
            {
              headers: headers,
            }
          )
          .pipe(
            map((result) => {
              return result as Provider;
            })
          );
      })
    );
  }
  public updateAboutMe(provider: Provider) {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        let url = "providers/updateAboutMe/" + provider.id;
        return this.http
          .post(
            this.env.API_URL + url,
            {
              about_me: provider.about_me,
            },
            {
              headers: headers,
            }
          )
          .pipe(
            map((result) => {
              return result as Provider;
            })
          );
      })
    );
  }
  public statsByStatus() {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        let url = "providers/providerStatsByStatus";
        return this.http
          .get(this.env.API_URL + url, {
            headers: headers,
          })
          .pipe(
            map((result) => {
              result = result as { [key: number]: number };
              let remoteData: RemoteStatData[] = [
                {
                  text: Provider.getStatTypeString(
                    ProviderStatsType.PROVIDER_STATS_TOTAL_COUNT
                  ),
                  number: result[ProviderStatsType.PROVIDER_STATS_TOTAL_COUNT],
                  url: null,
                  link_options: null,
                },
                {
                  text: Provider.getStatTypeString(
                    ProviderStatsType.PROVIDER_STATS_ONLINE_COUNT
                  ),
                  number: result[ProviderStatsType.PROVIDER_STATS_ONLINE_COUNT],
                  url: null,
                  link_options: null,
                },
                {
                  text: Provider.getStatTypeString(
                    ProviderStatsType.PROVIDER_STATS_IN_SESSION_COUNT
                  ),
                  number:
                    result[ProviderStatsType.PROVIDER_STATS_IN_SESSION_COUNT],
                  url: null,
                  link_options: null,
                },
                {
                  text: "آفلاین",
                  number:
                    result[ProviderStatsType.PROVIDER_STATS_TOTAL_COUNT] -
                    result[ProviderStatsType.PROVIDER_STATS_IN_SESSION_COUNT] -
                    result[ProviderStatsType.PROVIDER_STATS_ONLINE_COUNT],
                  url: null,
                  link_options: null,
                },
              ];
              this.statsByStatusShared.setResource(remoteData);
              return remoteData;
            })
          );
      })
    );
  }
}
