import { ProviderGroup } from './../../models/provider-group';
import { Component, OnInit, Input } from '@angular/core';
import { ModalController } from '@ionic/angular';

@Component({
  selector: 'app-select-provider-category',
  templateUrl: './select-provider-category.component.html',
  styleUrls: ['./select-provider-category.component.scss'],
})
export class SelectProviderCategoryComponent implements OnInit {
@Input() providerGroups:ProviderGroup[] = [];
  constructor(
    private modal:ModalController
  ) { }
  selectCategory(category:ProviderGroup)
  {
    this.dismiss(category);
  }
  ngOnInit() {
  }
  dismiss(data = null) {
    this.modal.dismiss(data);
  }
}
