import { CallComponent } from "./../common/call/call.component";
import { AlertService } from "src/app/services/alert.service";
import { User } from "src/app/models/user";
import { AlertController, Platform } from "@ionic/angular";
import { SmartAudioService } from "src/app/services/smart-audio.service";
import {
  VoiceCall,
  VoiceCallDirection,
  VoiceCallState,
} from "./../models/voice-call";
import { HttpHeaders } from "@angular/common/http";
import { EnvService } from "src/app/services/env.service";
import { HttpClient } from "@angular/common/http";
import { AuthService } from "src/app/services/auth.service";
import { Injectable } from "@angular/core";
import { from, Subject } from "rxjs";
import { concatMap, map, take, tap } from "rxjs/operators";
declare const KavenegarCall: any;
declare const Environment: any;
declare const CallDirection: any;

@Injectable({
  providedIn: "root",
})
export class CallService {
  kavenegarCall;
  innerCall = null;
  voiceCall: VoiceCall;
  callStateUpdated: Subject<VoiceCall> = new Subject<VoiceCall>();
  callFinished: Subject<VoiceCall> = new Subject<VoiceCall>();
  ringbackAudioHandle: HTMLAudioElement;
  ringingAudioHandle: HTMLAudioElement;
  cordovaCall = null;
  constructor(
    private auth: AuthService,
    private http: HttpClient,
    private env: EnvService,
    private smartAudio: SmartAudioService,
    private alert: AlertService,
    private platform: Platform
  ) {
    this.kavenegarCall = new KavenegarCall(Environment.PRODUCTION);
    
  }
  private setCallState(state: VoiceCallState) {
    if (state == VoiceCallState.RINGING && this.voiceCall.state != VoiceCallState.RINGING) {
      if (this.voiceCall.direction == VoiceCallDirection.OUTGOING) {
        this.smartAudio
        .play("ringback-tone", true)
        .pipe(take(1))
        .subscribe((element) => {
          this.ringbackAudioHandle = element;
        });
      } else {
        this.smartAudio
        .play("ringing", true)
        .pipe(take(1))
        .subscribe((element) => {
          this.ringingAudioHandle = element;
        });
      }
    }
    if (this.voiceCall.state == VoiceCallState.RINGING) {
      console.log(state);
      if (this.voiceCall.direction == VoiceCallDirection.OUTGOING) {
        if (this.ringbackAudioHandle) {
          this.smartAudio.pause(this.ringbackAudioHandle);
        }
      }
      else
      {
        if (this.ringingAudioHandle) {
          this.smartAudio.pause(this.ringingAudioHandle);
        }
      }
      
    }
    this.voiceCall.state = state;
    this.callStateUpdated.next(this.voiceCall);
  }
  call(receptor: User, maxDurationInMinutes: number) {
    this.auth.getUser().then((user) => {
      let call = new VoiceCall();
      call.receptor = receptor;
      call.caller = user;
      call.maxDuration = maxDurationInMinutes * 60;
      call.direction = VoiceCallDirection.OUTGOING;
      this.voiceCall = call;
      this.setCallState(VoiceCallState.NEW);
      this.alert
        .presentModal(CallComponent, { call: call },false)
        .pipe(take(1))
        .subscribe();
    });
  }
  receiveCall(call: VoiceCall) {
    this.auth.getUser().then((user) => {
      call.direction = VoiceCallDirection.INCOMING;
      call.receptor = user;
      this.voiceCall = call;
      this.setCallState(VoiceCallState.NEW);
      this.initiateConnectCall();
      this.alert
        .presentModal(CallComponent, { call: call },false)
        .pipe(take(1))
        .subscribe();
    });
  }
  makeTheCall() {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });
        let url = "makeCall";
        return this.http
          .post(
            this.env.API_URL + url,
            {
              receptor_user_id: this.voiceCall.receptor.id,
              max_duration: this.voiceCall.maxDuration,
            },
            {
              headers: headers,
            }
          )
          .pipe(
            map((result) => {
              return result as VoiceCall;
            }),
            tap((tempCall) => {
              this.voiceCall.id = tempCall.id;
              this.voiceCall.access_token = tempCall.access_token;
              this.setCallState(VoiceCallState.TRYING);
            })
          );
      })
    );
  }
  cancelCall() {
    this.voiceCall = null;
    this.innerCall = null;
  }
  acceptCall() {
    this.innerCall.accept();
    this.setCallState(VoiceCallState.ACCEPTED);
  }
  ring() {
    this.innerCall.ringing();
    this.setCallState(VoiceCallState.RINGING);
  }
  hangupCall() {
    this.setCallState(VoiceCallState.FINISHED);
    this.innerCall.hangup();
  }
  rejectCall() {
    this.setCallState(VoiceCallState.FLUSHED);
    this.innerCall.reject();
  }
  disposeCall() {
    console.log('dispose');
    if (this.innerCall) {
      console.log('dispose if');
      this.innerCall.dispose();
      this.callFinished.next(this.voiceCall);
     this.cancelCall;
    }
  }

  initiateConnectCall() {
    this.smartAudio.checkPermission().then((result) => {
      if (result) {
        this.connectCall();
      } else {
        this.smartAudio.askPermission().then(
          (result) => {
            if (result) {
              this.connectCall();
            } else {
              return;
            }
          },
          (reject) => {
            return;
          }
        );
      }
    });
  }
  private connectCall() {
    navigator.mediaDevices
      .getUserMedia({
        audio: true,
      })
      .then((stream) => {
        console.log("stream");
        const params = {
          callId: this.voiceCall.id,
          accessToken: this.voiceCall.access_token,
          localStream: stream,
          remoteElement: document.getElementById("audio2"),
        };
        this.kavenegarCall.initCall(params, (joinResult, call) => {
          if (call == null) {
            console.info("initCall was not successful :" + joinResult);
            return;
          }
          this.innerCall = call;
          if (call.direction === CallDirection.INBOUND) {
          }
          call.onFinished = (reason) => {};
          call.onMediaStateChanged = (event) => {
            if (call.direction === CallDirection.INBOUND) {
              if (call.receptorMediaStatus == "connected") {
                if (this.voiceCall.state == VoiceCallState.NEW) {
                  this.ring();
                  this.setCallState(VoiceCallState.RINGING);
                }
              }
            }
            console.info(
              "Remote Media Status => caller :" +
                call.callerMediaStatus +
                " , receptor : " +
                call.receptorMediaStatus
            );
          };
          call.onMessagingStateChanged = (event) => {
            console.info("Messaging Status : " + event);
          };
          call.onStatusChanged = (status) => {
            switch (status) {
              case "ringing":
                this.setCallState(VoiceCallState.RINGING);
                break;
              case "accepted":
                this.setCallState(VoiceCallState.ACCEPTED);
                break;
              case "conversation":
                this.setCallState(VoiceCallState.CONVERSATION);
                break;
              case "finished":
                this.setCallState(VoiceCallState.FINISHED);
                this.disposeCall();
                break;
              case "flushed":
                this.setCallState(VoiceCallState.FLUSHED);

                break;
            }

            console.info("Call Status : " + status);
          };
        });
      })
      .catch((error) => {
        alert("getUserMediaError :" + error);
      });
  }
}
