import { FileModel } from "./file-model";
import { User } from "./user";
import { Subject } from 'rxjs';
export class Message {
  topic: string;
  seq: string;
  from: string;
  fromMe: boolean = false;
  content: string;
  ts: Date;
  status: any;
  head: any;
  mime: any;
  attachments: MessageAttachment[] = null;
  isFirst = false;
  isLast = false;
  NotifyDirtySubject : Subject<Message>;
  getIsLast() {
    return this.isLast;
  }
  isImage: boolean = false;
  isAudio: boolean = false;
  file: FileModel = null;
  public notifyDirty()
{
  this.NotifyDirtySubject.next(this);
}
}
export class MessageAttachment {
  downloadUrl;
  mimeType;
  fileName;
}

export enum MessageStatus {
  MESSAGE_STATUS_NONE = 0, // Status not assigned.
  MESSAGE_STATUS_QUEUED = 1, // Local ID assigned, in progress to be sent.
  MESSAGE_STATUS_SENDING = 2, // Transmission started.
  MESSAGE_STATUS_FAILED = 3, // At least one attempt was made to send the message.
  MESSAGE_STATUS_SENT = 4, // Delivered to the server.
  MESSAGE_STATUS_RECEIVED = 5, // Received by the client.
  MESSAGE_STATUS_READ = 6, // Read by the user.
  MESSAGE_STATUS_TO_ME = 7 // Message from another user.
}
