import { catchError, concatMap, map } from "rxjs/operators";
import { AlertService } from "./../services/alert.service";
import { AuthService } from "./../services/auth.service";
import { Injectable } from "@angular/core";
import {
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
  UrlTree,
  CanActivate,
  Router,
} from "@angular/router";
import { Observable, from, of } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class HomeRedirectGuard implements CanActivate {
  constructor(
    private router: Router,
    private authService: AuthService,
    private alertService: AlertService
  ) {}
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean> | Promise<boolean> | boolean {
    return from(this.authService.isAuthenticated()).pipe(
      catchError((error) => {
        return of(false);
      }),
      concatMap((result) => {
        if (result) {
          return from(this.authService.isUser()).pipe(
            map((isUser) => {
              if (isUser) {
                this.router.navigateByUrl("/provider-categories");
              } else {
                from(this.authService.isAdmin()).subscribe(isAdmin=>{
                  if(isAdmin)
                  {
                    this.router.navigateByUrl("/adm-dashboard");
                  }
                  else
                  {
                    this.router.navigateByUrl("/pvd-reservations");
                  }
                });
                
              }
              return false;
            })
          );
          
        } else {
          this.router.navigateByUrl("/login");
        }
        return of(false);
      })
    );
  }
}
