import { SessionsManagerService } from "src/app/services/sessions-manager-service";
import { ChangePasswordComponent } from "./common/change-password/change-password.component";
import { debounceTime, take } from "rxjs/operators";
import { DepositComponent } from "./common/deposit/deposit.component";
import { UserService } from "src/app/services/user.service";
import { ProviderService } from "./services/provider.service";
import { ContactComponent } from "./common/static/contact/contact.component";
import { AboutComponent } from "./common/static/about/about.component";
import { TermsComponent } from "./common/static/terms/terms.component";
import { User } from "./models/user";
import { SmartAudioService } from "./services/smart-audio.service";
import { IMService } from "src/app/services/im.service";
import { catchError, concatMap } from "rxjs/operators";
import { from, of, Subject, Subscription, Observable, Subscriber } from "rxjs";
import { NotificationService } from "./services/notification.service";
import { EnvService } from "src/app/services/env.service";
import {
  Component,
  ComponentRef,
  ChangeDetectionStrategy,
} from "@angular/core";
import { MorphSVGPlugin } from "gsap/MorphSVGPlugin";

import {
  Platform,
  NavController,
  MenuController,
  ActionSheetController,
  ModalController,
  IonToggle,
} from "@ionic/angular";
import { StatusBar } from "@ionic-native/status-bar/ngx";
import { AuthService } from "./services/auth.service";
import { AlertService } from "./services/alert.service";
import { gsap, TimelineMax } from "gsap";
import { DrawSVGPlugin } from "gsap/DrawSVGPlugin";
import { SharedResourcesService } from './services/shared-resources.service';

@Component({
  selector: "app-root",
  templateUrl: "app.component.html",
  styleUrls: ["app.component.scss"],
})
export class AppComponent {
  user: User = null;
  userSubject: Subject<User> = new Subject<User>();
  getName = User.getName;

  userFetchedSubscription: Subscription;
  activitySwitch: boolean = null;
  roleDetectedSubscription: Subscription = null;
  splashing: boolean = true;
  constructor(
    private platform: Platform,
    private statusBar: StatusBar,
    private authService: AuthService,
    private navCtrl: NavController,
    private sharedResourceService:SharedResourcesService,
    private alertService: AlertService,
    private menu: MenuController,
    public env: EnvService,
    private notifactions: NotificationService,
    private imService: IMService,
    private smartAudio: SmartAudioService,
    private actionSheet: ActionSheetController,
    private modalController: ModalController,
    private providerService: ProviderService,
    public userService: UserService,
    public notifService: NotificationService,
    public sessionsManager: SessionsManagerService
  ) {
    this.splashCompleteCallback = this.splashCompleteCallback.bind(this);
    this.initializeApp();
    this.loadUserData();
    this.authService.loggedIn.subscribe((result) => {
      this.loadUserData();
    });
    this.authService.fetchedUser.subscribe((user) => {
      this.loadUserData();
    });
  }
  toggleActivitySwitch(toggle: IonToggle) {
    toggle.disabled = true;
    this.env.somethingIsPending("toggling provider activity switch");
    this.providerService
      .toggleActivitySwitch(!toggle.checked)
      .pipe(
        catchError((error) => {
          return of(false);
        })
      )
      .subscribe((result) => {
        this.env.somethingIsDonePending("toggling provider activity switch");
        toggle.disabled = false;
        if (!result) {
          toggle.checked = !toggle.checked;
        }
      });
  }
  loadUserData() {
    from(this.authService.isAuthenticated())
      .pipe(
        catchError((error) => {
          return of(false);
        })
      )
      .subscribe((result) => {
        if (result) {
          this.authService.getUser().then((result) => {
            this.user = result;
            this.userService.balanceShared.request();
            this.userSubject.next(result);
          });
        }
      });

    if (this.roleDetectedSubscription) {
      this.roleDetectedSubscription.unsubscribe();
    }
    this.roleDetectedSubscription = this.authService.roleDetected
      .pipe(debounceTime(1000))
      .subscribe((roleId) => {
        this.authService.isAuthenticated().then((result) => {
          if (result) {
            this.role = roleId;
            if (this.role == this.env.PROVIDER_ROLE_ID) {
              this.providerService
                .getActivitySwitch()
                .pipe(
                  catchError((error) => {
                    return of(-1);
                  })
                )
                .subscribe((result) => {
                  if (result != -1) {
                    this.activitySwitch = result as boolean;
                  }
                });
            }
          }
        });
      });
  }
  role: number = null;
  public appPages() {
    let menuItems = [];
    if (this.role != null) {
      if (this.role == this.env.USER_ROLE_ID) {
        menuItems = [
          {
            title: "درخواست مشاوره",
            url: "/provider-categories",
            icon: "home-outline",
          },
          {
            title: "مشاوره های من",
            url: "/usr-reservations",
            icon: "cart-outline",
          },
          {
            title: "تاریخچه درخواست ها",
            url: "/usr-request-log",
            icon: "archive-outline",
          },
          {
            title: "مشاورین نشان شده",
            url: "/usr/favorites",
            icon: "star-outline",
          },
          {
            title: "تخفیف",
            url: "/affiliate-code",
            icon: "color-filter-outline",
          },
          {
            title: "پرداخت ها", 
            url: `/user-payments/${this.user.id}`,
            icon: 'receipt-outline'
          }
        ];
      } else if (this.role == this.env.PROVIDER_ROLE_ID) {
        menuItems = [
          {
            title: "مشاوره های من",
            url: "/pvd-reservations",
            icon: "home-outline",
          },
          {
            title: "درخواست های جدید",
            url: "/pvd-active-requests",
            icon: "home-outline",
            badge_value: this.sessionsManager.myActiveRequestsShared.resource()
              .length,
            badge_condition:
              this.sessionsManager.myActiveRequestsShared.resource() &&
              this.sessionsManager.myActiveRequestsShared.resource().length > 0,
          },
          {
            title: "ساعات کاری",
            url: "/pvd-manage-available-hours",
            icon: "calendar-outline",
          },
          {
            title: "گزارش درآمد",
            url: "/pvd-income-report",
            icon: "calculator-outline",
          },
          {
            title: "تاریخچه درخواست ها",
            url: "/pvd-request-log",
            icon: "archive-outline",
          },
          {
            title: "پروفایل",
            url: "/profile",
            icon: "person-outline",
          },
        ];
      } else if (this.role == this.env.ADMIN_ROLE_ID) {
        menuItems = [
          {
            title: "داشبورد",
            url: "/adm-dashboard",
            icon: "home-outline",
          },
          {
            title: "کاربران",
            url: "/adm-users",
            icon: "people-outline",
          },
          {
            title: "مشاورین",
            url: "/adm-providers",
            icon: "list-outline",
          },
          {
            title: "پروفایل",
            url: "/profile",
            icon: "person-outline",
          },
        ];
      }
    }

    return menuItems;
  }
  ionViewWillEnter() {}
  ngOnInit(): void {
    if (!this.env.SPLASH_ENABLED) {
      this.splashing = false;
    }
  }
  profile() {
    if (this.user && this.user.role_id != this.env.USER_ROLE_ID) {
      this.navCtrl.navigateRoot(["/profile"]);
      this.menu.close();
    }
  }
  animateSplash() {
    if (!this.env.SPLASH_ENABLED) {
      this.splashing = false;
      return;
    }
    let animation = new Observable((observer) => {
      //gsap.registerPlugin(DrawSVGPlugin);
      const anim = new TimelineMax();
      gsap.registerPlugin(MorphSVGPlugin);
      var shapes = ".hands-svg";
      var tl = gsap.timeline();
      anim.to(
        "#frown-svg",
        {
          morphSVG: "#smile-svg",
          
          y: 0,
          duration: 2,
          ease: "power4.inOut",
        },
        1
      );

      anim.to(".eyes", { y: 0, ease: "bounce", duration: 1, stagger: 0.1 }, 3);
      anim.to(
        "#right-hand-svg",
        { opacity: 1, x: 10, y: 10, duration: 4, ease: "expo.out" },
        3
      );
      anim.to(
        "#left-hand-svg",
        { opacity: 1, x: -10, y: 10, duration: 4, ease: "expo.out" },
        3
      );
      anim
        .to(
          "#left-eye",
          { attr: { ry: 1 }, duration: 0.2, ease: "power4.in" },
          5
        )
        .to(
          "#left-eye",
          { attr: { ry: 15.5 }, ease: "power4.out", duration: 0.2 },
          5.3
        );
      anim.to(
        ".splash-svg",
        {
          opacity: 0,
          scale: 0.2,
          duration: 0.7,
          ease: "elastic.in(1, 1)",
          onComplete: this.splashCompleteCallback,
          onCompleteParams: [observer],
        },
        6
      );
      //anim.fromTo("#right-hand-svg", {opacity:0, x:100, y:100}, {opacity:1, x:0, y:0, duration:2},0)
    });
    animation.subscribe(null, null, () => {
      console.log("splash complete");
    });
  }
  splashCompleteCallback(toNext: Subscriber<unknown>) {
    this.splashing = false;
    toNext.next();
    toNext.complete();
  }
  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
    });
    this.smartAudio.preload("beep1", "assets/audio/beep1.mp3");
    this.smartAudio.preload("ringback-tone", "assets/audio/ringback-tone.ogg");
    this.smartAudio.preload("ringing", "assets/audio/ringing.mp3");
  }
  loadTermsPage() {
    this.loadStaticPage(TermsComponent, {providerPerspective:(this.role == this.env.PROVIDER_ROLE_ID)?true:false}).subscribe();
  }
  loadChangePassword() {
    this.alertService
      .presentModal(ChangePasswordComponent)
      .pipe(take(1))
      .subscribe();
  }
  loadQuestionAnswerPage() {
    this.navCtrl.navigateRoot(['/questions-and-answers',this.role,this.user.id]);
    this.menu.close();
  }
  loadAffiliatePage()
  {
    this.navCtrl.navigateRoot(['/affiliate-code']);
    this.menu.close();
  }

  loadAboutPage() {
    this.loadStaticPage(AboutComponent).subscribe();
  }
  loadContactPage() {
    this.loadStaticPage(ContactComponent).subscribe();
  }
  loadStaticPage(component: any, props = null) {
    return from(
      this.modalController.create({
        component: component,
        componentProps: props,
      })
    ).pipe(
      concatMap((modal) => {
        modal.onDidDismiss;
        from(modal.onDidDismiss()).subscribe((data) => {});
        return from(modal.present());
      })
    );
  }
  logout() {
    this.authService.logout();
    this.alertService.presentSuccess("از حساب کاربری خود خارج شده اید.");

    this.navCtrl.navigateRoot("/login");
  }
  initiateLogout() {
    this.presentLogoutConfirmation().subscribe();
  }
  presentLogoutConfirmation() {
    return from(
      this.actionSheet.create({
        mode: "ios",
        buttons: [
          {
            text: "خروج",
            role: "destructive",
            handler: () => {
              this.logout();
            },
          },
          {
            text: "انصراف",
            role: "cancel",
            handler: () => {},
          },
        ],
      })
    ).pipe(
      concatMap((modal) => {
        modal.onDidDismiss;
        from(modal.onDidDismiss()).subscribe((data) => {});
        return from(modal.present());
      })
    );
  }
  openAddCredit() {
    this.presentDepositModal().subscribe();
  }
  presentDepositModal() {
    return from(
      this.modalController.create({
        component: DepositComponent,
        componentProps: {},
      })
    ).pipe(
      concatMap((modal) => {
        modal.onDidDismiss;
        from(modal.onDidDismiss()).subscribe((data) => {});
        return from(modal.present());
      })
    );
  }
}
