import { NavController } from "@ionic/angular";
import { Component, OnInit, Input } from "@angular/core";

@Component({
  selector: "app-remote-stats-box",
  templateUrl: "./remote-stats-box.component.html",
  styleUrls: ["./remote-stats-box.component.scss"],
})
export class RemoteStatsBoxComponent implements OnInit {
  @Input() url;
  @Input() text: string;
  @Input() number: number;
  constructor(private navCtrl: NavController) {}

  ngOnInit() {}
  navigate() {
    if (this.url) {
      this.navCtrl.navigateForward(this.url);
    }
  }
}
