import { VoiceCall, VoiceCallDirection, VoiceCallState } from './../../models/voice-call';
import { catchError, takeUntil } from 'rxjs/operators';
import { User } from 'src/app/models/user';
import { CallService } from './../../services/call.service';
import { ModalController } from '@ionic/angular';
import { Component, OnInit, Input } from '@angular/core';
import { of, Subject } from 'rxjs';

@Component({
  selector: 'app-call',
  templateUrl: './call.component.html',
  styleUrls: ['./call.component.scss'],
})
export class CallComponent implements OnInit {
  call:VoiceCall = this.callService.voiceCall;
  callDirection = VoiceCallDirection;
  callState = VoiceCallState;
  callTimerSeconds;
  conversationSeconds = 0;
  ngUnsubscribe = new Subject();
  callTimerIntervalHanlde;
  constructor(
    private modalController:ModalController,
    private callService:CallService
  ) { }

  setCallTimerSeconds()
  {
    this.callTimerSeconds = new Date(1970, 0, 1).setSeconds(this.call.maxDuration - this.conversationSeconds);
  }
  ngOnInit() {
    
    if(this.call.direction == VoiceCallDirection.OUTGOING)
    {
      this.callService.makeTheCall().pipe(catchError(error=>{
        return of(-1);
      })).subscribe(result=>{
        if(result != -1)
        {
          this.callService.initiateConnectCall();
        }
        else
        {
          this.dismiss();
        }
      })
    }
    else 
    {
      
    }
    this.callService.callStateUpdated.pipe(takeUntil(this.ngUnsubscribe)).subscribe(()=>{
      switch(this.call.state)
      {
        case VoiceCallState.ACCEPTED:
          this.setCallTimerSeconds()
          break;
        case VoiceCallState.CONVERSATION:
          this.callTimerIntervalHanlde = setInterval(()=>{
            this.conversationSeconds ++;
            this.setCallTimerSeconds();
          }, 1000);
      }
      
    });
    this.callService.callFinished.pipe(takeUntil(this.ngUnsubscribe)).subscribe(()=>{
      console.log("finished subjec");
      this.dismiss();
      
    });
    
    
  }
  getCallStateText()
  {
    switch(this.call.state)
    {
      case VoiceCallState.NEW:
        return 'Connecting...';
      case VoiceCallState.TRYING:
        return 'Calling...';
      case VoiceCallState.RINGING:
        return 'Ringing...';
    }
  }
  connectIncoming()
  {
    this.callService.initiateConnectCall();
  }
  

  hangUp()
  {
    if(this.call.direction == VoiceCallDirection.INCOMING && this.call.state == VoiceCallState.RINGING)
    {
      this.callService.rejectCall();
    }
    else
    {
      this.callService.hangupCall();
    }
    
    this.dismiss();
  }
  accept()
  {
    this.callService.acceptCall();
  }
  dismiss()
  {
    this.modalController.dismiss();
  }
  ngOnDestroy(): void {
    clearInterval(this.callTimerIntervalHanlde);
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }
}
