import { AlertService } from 'src/app/services/alert.service';
import { PublicQuestionAndAnswersService } from './../../services/public-question-and-answers.service';
import { ModalController } from '@ionic/angular';
import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-answer',
  templateUrl: './answer.component.html',
  styleUrls: ['./answer.component.scss'],
})
export class AnswerComponent implements OnInit {

  @Input() questionId:number;
  answering:boolean = false;
  answerString:string ="";
  constructor(
    private modalController:ModalController,
    private qaService: PublicQuestionAndAnswersService,
    private alert:AlertService
  ) { }

  ngOnInit() {}
  dismiss()
  {
    this.modalController.dismiss();
  }
  answer()
  {
      this.answering = true;
      this.qaService.answer(this.answerString,this.questionId).subscribe(result=>{
        this.alert.presentSuccess("پاسخ با موفقیت اضافه شد");
        this.qaService.questionWithAnswersShared.request(this.questionId);
        this.answering = false;
        this.dismiss();
      })
    
    
  }
}
