import { SessionsManagerService } from "src/app/services/sessions-manager-service";
import { concatMap } from "rxjs/operators";
import { from } from "rxjs";
import { SessionInfoComponent } from "./../session-info/session-info.component";
import { ModalController, NavController } from "@ionic/angular";
import { User } from "./../../models/user";
import { EnvService } from "./../../services/env.service";
import { Session, SessionState, SessionType, SessionTimingType } from "src/app/models/session";
import { Component, OnInit, Input } from "@angular/core";
@Component({
  selector: "app-session-list-item-minimal",
  templateUrl: "./session-list-item-minimal.component.html",
  styleUrls: ["./session-list-item-minimal.component.scss"],
})
export class SessionListItemMinimalComponent implements OnInit {
  @Input() session: Session;
  @Input() providerPerspective: boolean;
  avatarUrl: string;
  peerName: string;
  user: User;
  stateText = Session.getStateString;
  sessionType = SessionType;
  sessionTimingTypeText = Session.getTimingTypeString;
  constructor(
    private env: EnvService,
    private modalController: ModalController,
    private sessionsManager: SessionsManagerService,
    private navController: NavController
  ) {}

  ngOnInit() {
    if (this.providerPerspective == true) {
      this.user = this.session.user;
      this.peerName = User.getName(this.session.user);
      this.avatarUrl = this.env.getPublicStorageUrl(this.session.user.avatar);
    } else {
      this.user = this.session.provider.user;
      this.peerName = User.getName(this.session.provider.user);
      this.avatarUrl = this.env.getPublicStorageUrl(
        this.session.provider.user.avatar
      );
    }
  }
  
  navigateToSession() {
    switch (Session.getState(this.session)) {
      case SessionState.ACTIVE:
      case SessionState.ENDED:
      case SessionState.REJECTED:
      case SessionState.REQUESTED:
      case SessionState.RESERVED:
        this.presentSessionInfoModel().subscribe();
    }
  }

  presentSessionInfoModel() {
    return from(
      this.modalController.create({
        component: SessionInfoComponent,
        componentProps: {
          session: this.session,
          providerPerspective: this.providerPerspective,
        },
      })
    ).pipe(
      concatMap((modal) => {
        modal.onDidDismiss;
        from(modal.onDidDismiss()).subscribe((data) => {});
        return from(modal.present());
      })
    );
  }
  getSessionDateTimeText() {
    switch (Session.getState(this.session)) {
      case SessionState.ACTIVE:
        return "زمان شروع جلسه";
      case SessionState.REQUESTED:
        return "زمان درخواست";
      case SessionState.RESERVED:
        return "زمان تأیید رزرو";
      case SessionState.REJECTED:
        return "زمان رد درخواست";
      case SessionState.ENDED:
        return "زمان پایان جلسه";
    }
  }

  
  getSessionTime() {
    return this.env.dateStringToJalaliTime(this.session.created_at);
    
  }
  getSessionDate() {
    return this.env.dateStringToJalaliDate(this.session.created_at);

  }
  getClass() {
    switch (Session.getState(this.session)) {
      case SessionState.ACTIVE:
        return "active";
      case SessionState.REQUESTED:
        return "requested";
      case SessionState.RESERVED:
        return "reserved";
      case SessionState.REJECTED:
        return "rejected";
      case SessionState.ENDED:
        return "ended";
    }
  }
  
  
}
