import {
  SharedResourceAsyncRequestBundle,
  SharedResourceUpdateStatus,
} from "src/app/architecture/shared-resource-async-request-bundle";
export class ArrayedSharedResource<
  T,
  Z
> extends SharedResourceAsyncRequestBundle<T[], Z> {
  private filterFunction: (value: T) => unknown = null;
  
  public filteredResource():T[]
  {
    if( this.filterFunction != null && this.resource())
        return this.resource().filter(this.filterFunction);
    return this.resource();
  }
  public setFilter(filterFn: (value: T) => unknown) {
    this.filterFunction = filterFn;
  }
  public needsNoItemAlert() {
    return (
      this.getResourceStatus() == SharedResourceUpdateStatus.UPDATED &&
      this.filteredResource().length < 1
    );
  }
}
