import { HttpHelperService } from './../services/http-helper.service';
import { EnvService } from 'src/app/services/env.service';
import { Component } from "@angular/core";
import { OnInit, OnDestroy, ComponentRef } from "@angular/core";
import { take, debounceTime, takeUntil, tap } from "rxjs/operators";
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { Subject, Observable } from "rxjs";
import { Storage } from "@ionic/storage";

export class SharedResourceAsyncRequestBundle<T, Z> {
  private requestHook: Subject<SharedResourceAsyncRequest> = new Subject();
  private WaitingQueue: SharedResourceAsyncRequest[] = [];
  private processingQueue: SharedResourceAsyncRequest[] = [];
  private processedCache: SharedResourceAsyncRequest[] = [];
  private iterationStatus: SharedResourceAsyncRequestIteratorStatus =
    SharedResourceAsyncRequestIteratorStatus.SLEEPING;
  private iterationSingleProcessComplete: Subject<SharedResourceAsyncRequest>;
  protected producer: (arg?: Z) => Observable<any>;
  private sharedResource: T;
  private updateNotifier: Subject<T> = new Subject();
  private resourceReadyNotifier: Subject<any> = new Subject();
  private resourceUpdateStatus: SharedResourceUpdateStatus =
    SharedResourceUpdateStatus.NOT_UPDATED;
  private cacheable = false;
  public static HttpHelper: HttpHelperService;
  public static Env: EnvService;
  public static Storage:Storage;
  constructor(
    producer: (arg?: Z) => Observable<any>,
    sharedResourceDefaultValue: T,
    ownerObject: any,
    cacheable = false
  ) {
    this.producer = producer;
    if(ownerObject)
    {
      this.producer = this.producer.bind(ownerObject) ;

    }
    this.sharedResource = sharedResourceDefaultValue;
    this.cacheable = cacheable;
    this.receiveRequests();
  }
  public getResourceStatus() {
    return this.resourceUpdateStatus;
  }
  public reset() {
    this.resourceUpdateStatus = SharedResourceUpdateStatus.NOT_UPDATED;
    this.processingQueue = [];
    this.iterationStatus = SharedResourceAsyncRequestIteratorStatus.SLEEPING;
    this.WaitingQueue = [];
    this.sharedResource = null;
    this.processedCache = [];
    this.updateNotifier.next(this.sharedResource);
  }
  public getUpdateNotifier(cleanUpComponent: OnInit = null): Observable<T> {
    let notifier = this.updateNotifier.pipe(tap());

    if (cleanUpComponent) {
    
      notifier = notifier.pipe(untilDestroyed(cleanUpComponent));
    }

    return notifier;
  }
  
  public needsSkeleton() {
    return (
      this.getResourceStatus() ==
      SharedResourceUpdateStatus.ATTEMPTING_FIRST_UPDATE
    );
  }
  public getResourceReadyNotifier(cleanUpComponent: OnDestroy = null): Observable<any> {
    let notifier = this.resourceReadyNotifier.pipe(tap());

    if (cleanUpComponent) {
      
      
      notifier = notifier.pipe(untilDestroyed(cleanUpComponent));
    }

    return notifier;
  }
  public setResource(value: T) {
    this.sharedResource = value;
    this.updateNotifier.next(value);
  }
  public resource(): T {
    return this.sharedResource;
  }
  private receiveRequests() {
    this.requestHook.pipe(debounceTime(1000)).subscribe((request) => {
      this.setRequestStatus(request, SharedResourceAsyncRequestStatus.AWAITING_PROCESS);
      this.WaitingQueue.push(request);
      this.pokeIterator();
    });
  }
 
  private iterate() {
    this.iterationSingleProcessComplete = new Subject();
    this.iterationSingleProcessComplete.subscribe((request) => {
      this.processingQueue.splice(this.processingQueue.indexOf(request), 1);
      this.processedCache.push(request);
      this.processNext();
    });
    this.processNext();
  }
  processNext() {
    if (this.WaitingQueue.length) {
      let request = this.WaitingQueue.pop();
      this.setRequestStatus(request, SharedResourceAsyncRequestStatus.PROCESSING);
      
      this.processingQueue.push(request);
      switch (this.resourceUpdateStatus) {
        case SharedResourceUpdateStatus.NOT_UPDATED:
          this.setStatus(SharedResourceUpdateStatus.ATTEMPTING_FIRST_UPDATE);
          break;
        case SharedResourceUpdateStatus.ERROR_ON_LAST_UPDATE_ATTEMPT:
          this.setStatus(SharedResourceUpdateStatus.ATTEMPTING_UPDATE_AFTER_ERROR);
          break;
        default:
         this.setStatus(SharedResourceUpdateStatus.ATTEMPTING_UPDATE);
      }

      this.produce(request)
        .subscribe((result) => {
          this.setStatus(SharedResourceUpdateStatus.UPDATED);
          this.resourceReadyNotifier.next();
          this.setRequestStatus(request, SharedResourceAsyncRequestStatus.COMPLETED);
          this.iterationSingleProcessComplete.next(request);
        });
    }
    this.iterationSingleProcessComplete.complete();
    this.iterationStatus = SharedResourceAsyncRequestIteratorStatus.SLEEPING;
  }
  private produce(request:SharedResourceAsyncRequest)
  {
    return this.producer(request.argument).pipe(take(1));
  }
  private pokeIterator() {
    if (
      this.iterationStatus == SharedResourceAsyncRequestIteratorStatus.SLEEPING
    ) {
      this.iterate();
    }
  }
  private setStatus(status:SharedResourceUpdateStatus)
  {
    
    this.resourceUpdateStatus = status;
  }
  private setRequestStatus(request:SharedResourceAsyncRequest,status:SharedResourceAsyncRequestStatus)
  {
    if(SharedResourceAsyncRequestBundle.Env)
    {
      switch(status)
      {
        case SharedResourceAsyncRequestStatus.COMPLETED:
        case SharedResourceAsyncRequestStatus.ERROR:
          SharedResourceAsyncRequestBundle.Env.somethingIsDonePending("Async Request: " + request.identifier);
          break;
        case SharedResourceAsyncRequestStatus.PROCESSING:
          SharedResourceAsyncRequestBundle.Env.somethingIsPending("Async Request: " + request.identifier);
      }
    }
    
    request.status = status;
  }
  request(argument?: Z): SharedResourceAsyncRequest {
    let request = new SharedResourceAsyncRequest();
    request.argument = argument;
    request.identifier = this.randomRequestIdentifier();
    request.time = new Date().getTime();

    this.requestHook.next(request);
    return request;
  }
  private randomRequestIdentifier(): number {
    return Math.random() * 10000 + 1;
  }
}
export enum SharedResourceUpdateStatus {
  NOT_UPDATED = 0,
  UPDATED = 1,
  ERROR_ON_LAST_UPDATE_ATTEMPT = 2,
  ATTEMPTING_FIRST_UPDATE = 3,
  ATTEMPTING_UPDATE = 4,
  ATTEMPTING_UPDATE_AFTER_ERROR = 5,
}
export class SharedResourceAsyncRequest {
  identifier: number;
  status: SharedResourceAsyncRequestStatus;
  time: number;
  argument: any;
}
enum SharedResourceAsyncRequestStatus {
  AWAITING_PROCESS = 0,
  PROCESSING = 1,
  COMPLETED = 2,
  ERROR = 3,
}
class SharedResourceAsyncRequestIterator {
  status: SharedResourceAsyncRequestIteratorStatus =
    SharedResourceAsyncRequestIteratorStatus.SLEEPING;
}
enum SharedResourceAsyncRequestIteratorStatus {
  BUSY = 0,
  HUNGRY = 1,
  SLEEPING = 2,
}
