import { EnvService } from 'src/app/services/env.service';
import { ProviderService } from 'src/app/services/provider.service';
import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-random-provider-avatars',
  templateUrl: './random-provider-avatars.component.html',
  styleUrls: ['./random-provider-avatars.component.scss'],
})
export class RandomProviderAvatarsComponent implements OnInit {
  @Input() categoryId:number = null;
  avatars:string[] = [];
  constructor(
    public providerService:ProviderService,
    public env:EnvService
  ) { }
    
  ngOnInit() {
    
    this.providerService.randomAvatarSrcListShared.request();
  }
  getAvatarZClass(index:number)
  {
    switch(index)
    {
      case 0:
      case 6:
        return "z-4";
      case 1:
      case 5:
        return "z-3";
      case 2:
      case 4:
        return "z-2";
      case 3:
        return "z-1"; 
    }
  }
  ngOnDestroy(): void {
  //Called once, before the instance is destroyed.
  //Add 'implements OnDestroy' to the class.
  
  }
}
