import { Session } from 'src/app/models/session';
import { DatePipe } from '@angular/common';
import { DaysOfWeek } from './../services/env.service';
export class AvailableHours {
    id:number;
    time_from:string;
    time_to:string;
    provider_id:number;
    expired:boolean;
    repeat:boolean;
    repeating_day_of_week:DaysOfWeek;
    disabled:boolean;
    overlappingSession:Session=null;
    reservedByMe = false;
    reserveAccepted = false;
    reserved = false;
    cssClass= "";
    public static fillCSSClass(hours:AvailableHours)
    {
        let c = "";
        if(hours.reserved)
        {
            c+= " reserved";
        }
        if(hours.reservedByMe)
        { 
            c+= " reserved-by-me";
        }
        if(hours.reserveAccepted)
        {
            c+= " reserve-accepted";
        }
        hours.cssClass = c;
    }
    public static getReservationStatusText(hours:AvailableHours)
    {
        let text = "قابل رزرو";
        if(hours.reserved)
        {
            text="پرشده"
        }
        if(hours.reservedByMe)
        { 
            text="درخواست داده شده";
        }
        if(hours.reserveAccepted)
        {
            text="رزرو شده";
        }
        return text;
    }
    public static getMinuteString(hourString:string)
    {
        return hourString.substr(3,2);
    }
    public static getHourString(hourString:string)
    {
        return hourString.substr(0,2);
    }
    public static getTimeFromHourString(hours:AvailableHours)
    {
        return AvailableHours.getHourString(hours.time_from);
    }
    public static getTimeToHourString(hours:AvailableHours)
    {
        return AvailableHours.getHourString(hours.time_to);
    }
    public static getTimeFromMinuteString(hours:AvailableHours)
    {
        return AvailableHours.getMinuteString(hours.time_from);
    }
    public static getTimeToMinuteString(hours:AvailableHours)
    {
        return AvailableHours.getMinuteString(hours.time_to);
    }
    public static SetTimeToBasedOnTimeFrom(hours:AvailableHours)
    {
        let pipe = new DatePipe("en");
        let hourFrom = parseInt( AvailableHours.getTimeFromHourString(hours));
        let minFrom =parseInt( AvailableHours.getTimeFromMinuteString(hours));
        let from = new Date().setUTCHours(hourFrom+1,minFrom);
        return hours.time_to = pipe.transform(from,"HH:mm",'UTC');

    }
    public static SetTimeNow(hours:AvailableHours)
    {
        let pipe = new DatePipe("en");
        hours.time_from = pipe.transform(new Date(),"HH:mm");
        AvailableHours.SetTimeToBasedOnTimeFrom(hours);
    }
    public static getMilitaryFormatInt(hourString:string){
        return parseInt(AvailableHours.getHourString(hourString) + AvailableHours.getMinuteString(hourString));
    }
}
