import { EnvService } from "src/app/services/env.service";
import { HttpClient } from "@angular/common/http";
import { HttpHeaders } from "@angular/common/http";
import { from } from "rxjs";
import { AuthService } from "src/app/services/auth.service";
import { PublicQuestionAndAnswer } from "./../models/public-question-and-answer";
import { SharedResourceAsyncRequestBundle } from "src/app/architecture/shared-resource-async-request-bundle";
import { Injectable } from "@angular/core";
import { concatMap, map } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class PublicQuestionAndAnswersService {
  public questionsShared: SharedResourceAsyncRequestBundle<
    PublicQuestionAndAnswer[],
    null
  >;
  public questionWithAnswersShared: SharedResourceAsyncRequestBundle<
    PublicQuestionAndAnswer,
    number
  >;
  public myQuestionsShared: SharedResourceAsyncRequestBundle<
    PublicQuestionAndAnswer[],
    null
  >;
  constructor(
    private auth: AuthService,
    private http: HttpClient,
    private env: EnvService
  ) {
    this.questionsShared = new SharedResourceAsyncRequestBundle<
      PublicQuestionAndAnswer[],
      null
    >(this.getAllQuestions, [], this);
    this.questionWithAnswersShared = new SharedResourceAsyncRequestBundle<
      PublicQuestionAndAnswer,
      number
    >(this.getAnswers, null, this);
    this.myQuestionsShared = new SharedResourceAsyncRequestBundle<
      PublicQuestionAndAnswer[],
      null
    >(this.getMyQuestions, [], this);
  }
  public getAllQuestions() {
    const headers = new HttpHeaders({
      Accept: "application/json",
      "Content-Type": "application/json",
    });

    let url = "questions/getAllQuestions";
    return this.http
      .get<PublicQuestionAndAnswer[]>(this.env.API_URL + url, {
        headers: headers,
      })
      .pipe(
        map((result) => {
          this.questionsShared.setResource(result);
          return result;
        })
      );
  }
  public getAnswers(questionId: number) {
    const headers = new HttpHeaders({
      Accept: "application/json",
      "Content-Type": "application/json",
    });

    let url = "questions/getAnswers/" + questionId;
    return this.http
      .get<PublicQuestionAndAnswer>(this.env.API_URL + url, {
        headers: headers,
      })
      .pipe(
        map((result) => {
          this.questionWithAnswersShared.setResource(result);
          return result;
        })
      );
  }
  public getMyQuestions() {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });

        let url = "questions/getMyQuestions";
        return this.http
          .get<PublicQuestionAndAnswer[]>(this.env.API_URL + url, {
            headers: headers,
          })
          .pipe(
            map((result) => {
              this.myQuestionsShared.setResource(result);
              return result;
            })
          );
      })
    );
  }
  public aks(content: string) {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });

        let url = "questions/ask";
        return this.http.post(
          this.env.API_URL + url,
          {
            content: content,
          },
          {
            headers: headers,
          }
        );
      })
    );
  }
  public answer(content: string, questionId: number) {
    return from(this.auth.getToken()).pipe(
      concatMap((token) => {
        const headers = new HttpHeaders({
          Authorization: token["token_type"] + " " + token["access_token"],
          Accept: "application/json",
          "Content-Type": "application/json",
        });

        let url = "questions/answer";
        return this.http.post(
          this.env.API_URL + url,
          {
            content: content,
            question_id: questionId,
          },
          {
            headers: headers,
          }
        );
      })
    );
  }
}
