import { async } from "@angular/core/testing";
import { EnvService } from "src/app/services/env.service";
import { PickerController } from "@ionic/angular";
import { Component, OnInit, Input } from "@angular/core";
import moment, * as jalaliMoment from "jalali-moment";
import { DatePipe, getLocaleMonthNames } from "@angular/common";
import { ModalController } from "@ionic/angular";
import { from } from "rxjs";
import { concatMap } from "rxjs/operators";
@Component({
  selector: "app-date-picker",
  templateUrl: "./date-picker.component.html",
  styleUrls: ["./date-picker.component.scss"],
})
export class DatePickerComponent implements OnInit {
  @Input() date: Date;
  yearValue: number = 1;
  month: number = 1;
  day: number = 1;
  pickerType = PickerType.YEAR;
  pickerElement: HTMLIonPickerElement;
  constructor(
    private modalController: ModalController,
    private picker: PickerController,
    private env: EnvService
  ) {}

  ngOnInit() {}
  ionViewWillEnter() {
    let tempDateString = this.env.generateUTCString(this.date, "yyyy/MM/dd");
    this.setYear(this.env.dateStringToJalaliYear(tempDateString));
    this.month = this.env.dateStringToJalaliMonth(tempDateString);
    this.day = this.env.dateStringToJalaliDay(tempDateString);
  }
  dismiss() {
    this.modalController.dismiss();
  }
  confirm() {
    this.modalController.dismiss({
      date: new Date(
        jalaliMoment(
          this.getYear() + "/" + this.month + "/" + this.day,
          "jYYYY/jM/jD"
        ).toLocaleString()
      ),
    });
  }
  select(pickerType: PickerType) {
    this.pickerType = pickerType;
    this.openPicker().subscribe();
  }

  openPicker() {
    return from(
      this.picker.create({
        columns: this.getColumns(),
        buttons: [
          {
            text: "انصراف",
            role: "cancel",
          },
          {
            text: "تأیید",
            handler: (value) => {
              this.setValue(value["col-1"]["value"]);
            },
          },
        ],
        mode: "ios",
      })
    ).pipe(
      concatMap((picker) => {
        picker.addEventListener("ionPickerColChange", async (event: any) => {
        });
        picker.columns[0].selectedIndex = this.getSelectedIndex();
        return from(picker.present());
      })
    );
  }
  getSelectedIndex() {
    switch (this.pickerType) {
      case PickerType.YEAR:
        return this.yearValue - 1;
      case PickerType.MONTH:
        return this.month - 1;
      case PickerType.DAY:
        return this.day - 1;
    }
  }
  setValue(val) {
    switch (this.pickerType) {
      case PickerType.YEAR:
        this.yearValue = val;
        this.select(PickerType.MONTH);
        break;
      case PickerType.MONTH:
        this.month = val;
        this.select(PickerType.DAY);
        break;
      case PickerType.DAY:
        this.day = val;
        break;
    }
  }
  getMonthName() {
    return this.env.getMonthName(this.month);
  }
  setYear(year) {
    this.yearValue = year - 1398;
  }
  getYear(value = this.yearValue) {
    return value + 1398;
  }
  getYears() {
    let options = [];
    for (let i = 1; i < 3; i++) {
      options.push({
        text: this.getYear(i),
        value: i,
      });
    }
    return options;
  }
  getMonths() {
    let options = [];
    let selected = false;
    for (let i = 1; i < 13; i++) {
      options.push({
        text: this.env.getMonthName(i),
        value: i,
      });
    }
    return options;
  }

  getDayes() {
    let options = [];
    let dateString = this.getYear() + "/" + this.month;
    for (
      let i = 1;
      i <= jalaliMoment(dateString, "jYYYY/jM").jDaysInMonth();
      i++
    ) {
      options.push({
        text: i,
        value: i,
      });
    }
    return options;
  }
  getColumns() {
    let columns = [];
    let options = [];
    switch (this.pickerType) {
      case PickerType.YEAR:
        options = this.getYears();
        break;
      case PickerType.MONTH:
        options = this.getMonths();
        break;
      case PickerType.DAY:
        options = this.getDayes();
        break;
    }

    columns.push({
      name: "col-1",
      options: options,
    });
    return columns;
  }
}
enum PickerType {
  YEAR = 0,
  MONTH = 1,
  DAY = 2,
}
