import { HttpHelperService } from './http-helper.service';
import { AutoSingleGetResource } from './../architecture/auto-single-get-resource';
import { SingleSharedResource } from './../architecture/single-shared-resource';
import { IMService } from "src/app/services/im.service";
import { Observable, of, from, Subject, ReplaySubject, throwError } from "rxjs";
import { catchError, concatMap } from "rxjs/operators";
import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { BehaviorSubject } from "rxjs";
import { tap, mergeMap, map } from "rxjs/operators";
import { NativeStorage } from "@ionic-native/native-storage/ngx";
import { EnvService } from "./env.service";
import { User } from "../models/user";
import { Storage } from "@ionic/storage";
import { FirebaseX } from "@ionic-native/firebase-x/ngx";
@Injectable({
  providedIn: "root",
})
export class AuthService {
  token: any;
  user: User = null;
  role: number;
  fetchedUser: Subject<User> = new Subject<User>();
  loggedIn: Subject<boolean> = new Subject<boolean>();
  roleDetected: ReplaySubject<number> = new ReplaySubject<number>();
  loggedOut: Subject<boolean> = new Subject();
  loggedOutExToken: Subject<any> = new Subject();
  tempPhoneNumber = null;
  tempPassword = null;
  currentUserShared = new AutoSingleGetResource<User>('user');
  constructor(
    private http: HttpClient,
    private storage: Storage,
    private env: EnvService
  ) {
    this.role = this.env.USER_ROLE_ID;
  }

  login(phone: String, password: String, roleId: number) {
    return this.http
      .post(this.env.API_URL + "login", {
        username: phone,
        password: password,
        client_id: this.env.CLIENT_ID,
        grant_type: "password",
        client_secret: this.env.CLIENT_SECRET,
        role_id: roleId,
      })
      .pipe(
        tap((access_token) => {
          this.saveToken(access_token).then((result) => {
            this.saveUser();
          });
          this.loggedIn.next(true);
          this.token = access_token;
          return access_token;
        })
      );
  }
  remoteSetFcmToken(fcmToken) {
    let url = "setFcmToken";
    return from(this.getToken()).pipe(
      concatMap((token) => {
        return this.http.post(
          this.env.API_URL + url,
          {
            fcm_token: fcmToken,
          },
          {
            headers: {
              Authorization: token["token_type"] + " " + token["access_token"],
              Accept: "application/json",
              "Content-Type": "application/json",
            },
          }
        );
      })
    );
  }
  remoteUnsetFcmToken(authExToken) {
    let url = "unsetFcmToken";

    return this.http.post(this.env.API_URL + url, null, {
      headers: {
        Authorization:
          authExToken["token_type"] + " " + authExToken["access_token"],
        Accept: "application/json",
        "Content-Type": "application/json",
      },
    });
  }
  changePassword(oldPass, newPass) {
    return from(this.getToken()).pipe(
      concatMap((token) => {
        return this.http.post(
          this.env.API_URL + "changePassword",
          {
            old_password: oldPass,
            new_password: newPass,
          },
          {
            headers: {
              Authorization: token["token_type"] + " " + token["access_token"],
              Accept: "application/json",
              "Content-Type": "application/json",
            },
          }
        );
      })
    );
  }
  updateChatCredentials(username: string, pass: string, uid: string) {
    return from(this.getToken()).pipe(
      concatMap((token) => {
        return this.http.post(
          this.env.API_URL + "updateChatCredentials",
          {
            tinode_username: username,
            tinode_pass: pass,
            tinode_uid: uid,
          },
          {
            headers: {
              Authorization: token["token_type"] + " " + token["access_token"],
              Accept: "application/json",
              "Content-Type": "application/json",
            },
          }
        );
      })
    );
  }
  register(
    phone: string,
    fName: string,
    lName: string,
    password: string,
    roleId = this.env.USER_ROLE_ID,
    affiliateCode:string
  ) {
    return this.http.post(this.env.API_URL + "register", {
      phone: phone,
      password: password,
      client_id: this.env.CLIENT_ID,
      client_secret: this.env.CLIENT_SECRET,
      first_name: fName,
      role_id: roleId,
      last_name: lName,
      affiliate_code:affiliateCode
    });
    //return of(true);
    /* return from(this.imService.createAccount(phone, password)).pipe(
      catchError(error=>{
        if(error.message == 'duplicate credential (409)')
        {
          return of(false);
        }
        throw "problem creating tinode account";
        
      }),
      mergeMap(data => {
        return this.http.post(this.env.API_URL + "register", {
          phone: phone,
          password: password,
          tinode_username: phone,
          tinode_pass: password,
          tinode_uid: data,
          client_id: this.env.CLIENT_ID,
          client_secret: this.env.CLIENT_SECRET,
          first_name: fName,
          last_name: lName
        });
      })
    ); */
  }
  requestVerificationCode(
    phone: string,
    password: string
  ): Observable<boolean> {
    return this.http
      .post(this.env.API_URL + "requestVerificationCode", {
        phone: phone,
        password: password,
      })
      .pipe(
        map((result) => {
          if (result["success"] == true) {
            return true;
          }
        })
      );
  }
  requestVerificationCodeForPasswordRetrieval(
    phone: string,
  ): Observable<boolean> {
    return this.http
      .post(this.env.API_URL + "requestVerificationCodeForPasswordRetrieval", {
        phone: phone,
      })
      .pipe(
        map((result) => {
          if (result["success"] == true) {
            return true;
          }
        })
      );
  }
  retrievePassword(
    phone: string,
    password: string,
    verificationCode
  ) {
    return this.http
      .post(this.env.API_URL + "retrievePassword", {
        phone: phone,
        password: password,
        verification_code:verificationCode
      });
  }
  verify(verificationCode, phoneNumber) {
    return this.http
      .post(this.env.API_URL + "verify", {
        phone: phoneNumber,
        verification_code: verificationCode,
      })
      .pipe(
        map((result) => {
          if (result["success"] == true) {
            return true;
          }
        })
      );
  }
  getUserRemote() {
    const headers = new HttpHeaders({
      Authorization:
        this.token["token_type"] + " " + this.token["access_token"],
    });
    return this.http
      .get<User>(this.env.API_URL + "user", { headers: headers })
      .pipe(
        map((user) => {
          this.user = user as User;
          this.saveUser();
          this.roleDetected.next(user.role_id);
          this.fetchedUser.next(user as User);
          return user;
        })
      );
  }

  isAuthenticated() {
    return this.getToken()
      .then()
      .catch((err) => {
        throwError("err");
      });
  }
  logout() {
    return from(this.getToken()).subscribe((token) => {
      this.loggedOutExToken.next(token);
      this.storage.remove("token");
      this.storage.remove("user");
      delete this.token;
      delete this.user;
      this.loggedOut.next(true);
    });
  }
  isProvider() {
    return this.getUser()
      .then((user) => {
        if ((user as User).role_id == this.env.PROVIDER_ROLE_ID) {
          this.role = this.env.PROVIDER_ROLE_ID;
          return true;
        }

        return false;
      })
      .catch((error) => {
        return Promise.reject(error);
      });
  }
  isAdmin() {
    return this.getUser()
      .then((user) => {
        if ((user as User).role_id == this.env.ADMIN_ROLE_ID) {
          this.role = this.env.ADMIN_ROLE_ID;
          return true;
        }

        return false;
      })
      .catch((error) => {
        return Promise.reject(error);
      });
  }
  isUser() {
    return this.getUser()
      .then((user) => {
        if ((user as User).role_id == this.env.USER_ROLE_ID) {
          this.role = this.env.USER_ROLE_ID;
          return true;
        }
        this.role = this.env.PROVIDER_ROLE_ID;
        return false;
      })
      .catch((error) => {
        return Promise.reject(error);
      });
  }
  getUser(): Promise<any> {
    return this.isAuthenticated()
      .then(
        (token) => {
          if (token) {
            if (this.user && this.user != null) {
              this.roleDetected.next(this.user.role_id);
              return Promise.resolve(this.user);
            } else {
              return this.storage.get("user").then(
                (user) => {
                  if (user != null) {
                    let parsedUser = JSON.parse(user);
                    this.user = parsedUser;
                    this.roleDetected.next(this.user.role_id);
                    return Promise.resolve(this.user);
                  } else {
                    return this.getUserRemote()
                      .pipe(
                        catchError((error) => {
                          return Promise.reject(null);
                        })
                      )
                      .toPromise();
                  }
                },
                (error) => {
                  return this.getUserRemote();
                }
              );
            }
          } else {
            return Promise.reject(false);
          }
        },
        (error) => {
          return Promise.reject(false);
        }
      )
      .catch((error) => {
        return Promise.reject(error);
      });
  }
  saveUser() {
    return this.getUser().then((user) => {
      return this.storage.set("user", JSON.stringify(user));
    });
  }
  saveToken(access_token) {
    return this.storage.set("token", access_token);
  }
  getToken() {
    if (this.token && this.token != null) {
      return Promise.resolve(this.token);
    } else {
      return this.storage.get("token").then((token) => {
        if (token != null) {
          this.token = token;
          return Promise.resolve(this.token);
        } else {
          return Promise.reject("token not found");
        }
      });
    }
  }
}
