import { Subscription } from 'src/app/models/subscription';
import { Model } from './model';
import { AdditionalInfo } from './additional-info';
export class User extends Model{
    id: number;
    first_name: string;
    last_name: string;
    phone: string;
    tinode_uid:string;
    tinode_username:string;
    tinode_pass:string;
    avatar:string;
    avatar_thumbnail:string;
    role_id:number;
    additional_info:AdditionalInfo;
    p2p_subscriptions:Subscription[];
    session_subscriptions:Subscription[];
    must_subscriptions:Subscription[];
    public static getName(user:User)
    {   
        if(user.role_id != RoleId.USER_ROLE_ID)
        {
            return user.first_name + ' ' + user.last_name;
        }
        return user.phone;
        
    }
    public static getRoleName(user:User)
    {
        if(user.role_id == User.PROVIDER_ROLE_ID )
        {
            return 'مشاور';
        }
        if(user.role_id == User.USER_ROLE_ID )
        {
            return 'کاربر';
        }
        if(user.role_id == User.ADMIN_ROLE_ID )
        {
            return 'مدیر';
        }
    }
    public static PROVIDER_ROLE_ID = 1;
    public static USER_ROLE_ID = 2;
    public static ADMIN_ROLE_ID = 3;
}
export enum UserStatsType {
    USER_STATS_TOTAL_COUNT = 2,
    USER_STATS_VERIFIED_COUNT = 1
  }
export enum RoleId
{
    USER_ROLE_ID = 2,
    PROVIDER_ROLE_ID = 1,
    ADMIN_ROLE_ID = 3
}