import { User } from 'src/app/models/user';
export class VoiceCall {
    caller:User;
    receptor:User;
    id;
    access_token;
    maxDuration:number;
    direction:VoiceCallDirection;
    state:VoiceCallState;
}
export enum VoiceCallDirection
{
    OUTGOING = 1,
    INCOMING = 2,
}
export enum VoiceCallState
{
    NEW= 1,
    TRYING = 2,
    RINGING = 3,
    ACCEPTED = 4,
    CONVERSATION = 5,
    PAUSED = 6,
    FINISHED = 7,
    FLUSHED =  8
}
