import { User } from './../../models/user';
import { NavController } from '@ionic/angular';
import { ProviderGroup } from './../../models/provider-group';
import { Provider } from 'src/app/models/provider';
import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-provider-list-item',
  templateUrl: './provider-list-item.component.html',
  styleUrls: ['./provider-list-item.component.scss'],
})
export class ProviderListItemComponent implements OnInit {
  @Input() provider:Provider = null;
  @Input() category:ProviderGroup = null;
  @Input() providerProfileUrl = '/provider';
  @Input() isFavorite:boolean = false;
  getStatusClass = Provider.getStatusClass;
  getStatusText = Provider.getStatusText;
  getDegreeText = Provider.getDegreeString;
  getName = User.getName;
  
  constructor(
    private navController:NavController
  ) { }

  ngOnInit() {}
  navigateToProvider(provider:Provider)
  {
    this.navController.navigateForward([this.providerProfileUrl, provider.id]);
  }
}
