import { Provider } from "src/app/models/provider";
import { AvailableHours } from "./available-hours";
import { User } from "./user";
export class Session {
  id: number;
  provider: Provider;
  online: boolean;
  started: Date;
  ended: Date;
  created_at: Date;
  accepted: Date;
  user: User;
  per_minute_fee: number;
  chat_topic_name;
  reserved_from: Date;
  reserved_to: Date;
  mock_session: boolean = false;
  duration: number;
  type: number;
  user_id: number;
  timing_type: SessionTimingType;
  score:number;
  public static getState(session) {
    if (session.accepted == null && session.ended == null) {
      return SessionState.REQUESTED;
    }
    if (session.accepted != null && session.started == null) {
      return SessionState.RESERVED;
    }
    if (session.started != null && session.ended == null) {
      return SessionState.ACTIVE;
    }
    if (session.accepted != null && session.ended != null) {
      return SessionState.ENDED;
    }
    if (session.accepted == null && session.ended != null) {
      return SessionState.REJECTED;
    }
  }
  public static getTimingTypeString(session: Session) {
    if (session.timing_type == SessionTimingType.RESERVATION) {
        return 'رزرو';

    }
    if(session.timing_type == SessionTimingType.IMMEDIATE)
    {
        return 'آنی';
    }
  }
  public static getStateString(session: Session) {
    const state = Session.getState(session);
    return Session.getStateStringByState(state);
  }
  public static getStateStringByState(state: SessionState) {
    switch (state) {
      case SessionState.ACTIVE:
        return "باز";
      case SessionState.RESERVED:
        return "در انتظار شروع";
      case SessionState.ENDED:
        return "پایان یافته";
      case SessionState.REJECTED:
        return "رد شده";
      case SessionState.REQUESTED:
        return "درخواست شده";
    }
  }
  public static isActive(session: Session) {
    return Session.getState(session) == SessionState.ACTIVE;
  }
  public static isEnded(session: Session) {
    return Session.getState(session) == SessionState.ENDED;
  }
  public static isRequested(session: Session) {
    return Session.getState(session) == SessionState.REQUESTED;
  }
  public static isReserved(session: Session) {
    return Session.getState(session) == SessionState.RESERVED;
  }
  public static isRejected(session: Session) {
    return Session.getState(session) == SessionState.REJECTED;
  }
  public static createMockSession(
    hours: AvailableHours,
    date: Date,
    provider: Provider,
    duration: number,
    type: number,
    timingType:SessionTimingType
  ) {
      if(hours == null)
      {
          hours = new AvailableHours();
          AvailableHours.SetTimeNow(hours);
          
      }
    let fromDate = new Date(
      date.getFullYear(),
      date.getMonth(),
      date.getDate(),
      parseInt(AvailableHours.getTimeFromHourString(hours)),
      parseInt(AvailableHours.getTimeFromMinuteString(hours))
    );
    let toDate = new Date(
      date.getFullYear(),
      date.getMonth(),
      date.getDate(),
      parseInt(AvailableHours.getTimeToHourString(hours)),
      parseInt(AvailableHours.getTimeToMinuteString(hours))
    );

    let session: unknown = {
      mock_session: true,
      reserved_from: fromDate.toUTCString(),
      reserved_to: toDate.toUTCString(),
      created_at: new Date().toUTCString(),
      duration: duration,
      provider: provider,
      type: type,
      timing_type: timingType
    };

    return session as Session;
  }
}

export enum SessionState {
  REQUESTED = 0,
  RESERVED = 1,
  ACTIVE = 2,
  ENDED = 3,
  REJECTED = 4,
}
export enum SessionType {
  TEXT = 1,
  Call = 2,
}
export enum SessionTimingType {
  IMMEDIATE = 1,
  RESERVATION = 0,
}
