import { SessionType, SessionTimingType } from 'src/app/models/session';
import { ScoreComponent } from "./../score/score.component";
import { catchError, concatMap } from "rxjs/operators";
import { of, from } from "rxjs";
import { AlertService } from "./../../services/alert.service";
import { User } from "./../../models/user";
import { SessionsManagerService } from "./../../services/sessions-manager-service";
import { EnvService } from "./../../services/env.service";
import { ModalController, NavController } from "@ionic/angular";
import { Session, SessionState } from "./../../models/session";
import { Component, OnInit, Input } from "@angular/core";
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: "app-session-info",
  templateUrl: "./session-info.component.html",
  styleUrls: ["./session-info.component.scss"],
})
export class SessionInfoComponent implements OnInit {
  @Input() session: Session;
  @Input() providerPerspective: boolean;
  @Input() openedFromSessionChat: boolean = false;
  sessionTypeEnum = SessionType;
  avatarUrl: string;
  peerName: string;
  user: User = null;
  waiting: boolean = false;
  constructor(
    private modal: ModalController,
    private env: EnvService,
    private sessionsManager: SessionsManagerService,
    private alert: AlertService,
    private nav: NavController,
    private modalController: ModalController
  ) {}

  ngOnInit() {
    if (this.providerPerspective) {
      this.peerName = User.getName(this.session.user);
      this.user = this.session.user;
      this.avatarUrl = this.env.getPublicStorageUrl(this.session.user.avatar);
    } else {
      this.user = this.session.provider.user;
      this.peerName = User.getName(this.session.provider.user);
      this.avatarUrl = this.env.getPublicStorageUrl(
        this.session.provider.user.avatar
      );
    }
  }
  getSessionState() {
    return Session.getStateString(this.session);
  }
  getSessionTimingType()
  {
    return Session.getTimingTypeString(this.session);
  }
  dismiss() {
    this.modal.dismiss();
  }
  getSessionTime(when: string) {
    switch (when) {
      case "started":
        return this.env.dateStringToJalaliTime(this.session.started);
      case "created":
        return this.env.dateStringToJalaliTime(this.session.created_at);
      case "accepted":
        return this.env.dateStringToJalaliTime(this.session.accepted);
      case "rejected":
      case "ended":
        return this.env.dateStringToJalaliTime(this.session.ended);
      case "requested":
        return this.env.dateStringToJalaliTime(this.session.reserved_from);
      case "requestedTo":
        return this.env.dateStringToJalaliTime(this.session.reserved_to);
    }
  }
  getSessionDate(when: string) {
    switch (when) {
      case "started":
        return this.env.dateStringToJalaliDate(this.session.started);
      case "created":
        return this.env.dateStringToJalaliDate(this.session.created_at);
      case "accepted":
        return this.env.dateStringToJalaliDate(this.session.accepted);
      case "rejected":
      case "ended":
        return this.env.dateStringToJalaliDate(this.session.ended);
      case "requested":
        return this.env.dateStringToJalaliDate(this.session.reserved_from);
      case "requestedTo":
        return this.env.dateStringToJalaliDate(this.session.reserved_to);
    }
  }
  isMock() {
    return this.session.mock_session;
  }
  isRequested() {
    return Session.isRequested(this.session);
  }
  isReserved() {
    return Session.isReserved(this.session);
  }
  isActive() {
    return Session.isActive(this.session);
  }
  isEnded() {
    return Session.isEnded(this.session);
  }
  isRejected() {
    return Session.isRejected(this.session);
  }
  accept() {
    this.sessionsManager
      .acceptSession(this.session)
      .pipe(
        catchError(error => {
          this.alert.presentError("عملیات با مشکل روبرو شد.");
          return of(-1);
        })
      )
      .subscribe((result) => {
        if (result != -1) {
          if(this.session.timing_type == SessionTimingType.IMMEDIATE)
          {
            this.nav.navigateRoot(['/pvd-reservations']);
          }
          this.alert.presentSuccess("درخواست رزرو تأیید شد.");
        }

        this.dismiss();
      });
  }
  reject() {
    this.sessionsManager
      .rejectSession(this.session)
      .pipe(
        catchError(() => {
          this.alert.presentError("عملیات با مشکل روبرو شد.");
          return of(null);
        })
      )
      .subscribe((result) => {
        if (result != null) {
          this.alert.presentSuccess("درخواست رزرو رد شد.");
        }

        this.dismiss();
      });
  }
  start() {
    this.sessionsManager
      .startSession(this.session)
      .pipe(
        catchError(() => {
          this.alert.presentError("عملیات با مشکل روبرو شد.");
          return of(null);
        })
      )
      .subscribe((result) => {
        if (result != null) {
          this.alert.presentSuccess("جلسه شروع شد.");
        }

        this.dismiss();
      });
  }
  end() {
    this.sessionsManager
      .rejectSession(this.session)
      .pipe(
        catchError(() => {
          this.alert.presentError("عملیات با مشکل روبرو شد.");
          return of(null);
        })
      )
      .subscribe((result) => {
        if (result != null) {
          this.alert.presentSuccess("جلسه پایان یافت.");
        }

        this.dismiss();
      });
  }
  request() {
    this.waiting = true;
    this.sessionsManager
      .chechRequestEligibility(
        this.session.provider.id,
        this.session.reserved_from,
        this.session.reserved_to,
        this.session.duration,
        this.session.type,
        this.session.timing_type
      )
      .pipe(
        catchError((error:HttpErrorResponse) => {
          this.alert.presentError(this.env.serverErrorMessage(error.error['error_code']));
          return of(-1);
        })
      )
      .subscribe((result) => {
        if (result != -1) {
          this.sessionsManager
            .usrRequestSession(
              this.session.provider.id,
              this.session.reserved_from,
              this.session.reserved_to,
              this.session.duration,
              this.session.type,
              this.session.timing_type
            )
            .pipe(
              catchError((error) => {
                return of(-1);
              })
            )
            .subscribe((result) => {
              this.waiting = false;
              if (result == -1) {
                this.alert.presentError(
                  "درخواست جلسه مشاوره با مشکل روبرو شد."
                );
              } else if (result != null) {
                this.alert.presentSuccess("درخواست جلسه مشاوره ارسال شد.");
                this.nav.navigateBack(["usr-request-log"]);
              } else {
                this.alert.presentError(
                  "قبلا یک در خواست پاسخ داده نشده دارید."
                );
              }
              this.dismiss();
              //this.nav.navigateBack(["provider/" + this.session.provider.id]);
              
            });
        }
        else
        {
          this.dismiss();
        }
      });
  }
  calculateSessionDuration() {
    return this.sessionsManager.calculateSessionDuration(this.session);
  }
  calculateSessionTotalFee() {
    return this.sessionsManager.calculateSessionTotalFee(this.session);
  }
  chatHistory() {
    if (this.providerPerspective) {
      this.sessionsManager.pvdStartSessionChat(this.session);
    } else {
      this.sessionsManager.usrStartSessionChat(this.session);
    }
    this.dismiss();
  }
  score() {
    if (!this.session.score) {
      this.sessionsManager.openScoreSession(this.session, this.session.score, (score)=>{
        this.session.score = score;
      },this)
    }
  }
  
}
