import { AlertService } from 'src/app/services/alert.service';
import { Fee } from './../../models/fee';
import { SelectProviderCategoryComponent } from "./../select-provider-category/select-provider-category.component";
import { ProviderGroup } from "./../../models/provider-group";
import { ProviderService } from "src/app/services/provider.service";
import { EnvService } from "src/app/services/env.service";
import { Provider, EducationalDegree } from "src/app/models/provider";
import { Component, OnInit, Input } from "@angular/core";
import { ModalController, ActionSheetController } from "@ionic/angular";
import { map, concatMap, catchError } from "rxjs/operators";
import { from, of } from "rxjs";

@Component({
  selector: "app-provider-info-edit",
  templateUrl: "./provider-info-edit.component.html",
  styleUrls: ["./provider-info-edit.component.scss"],
})
export class ProviderInfoEditComponent implements OnInit {
  @Input() editable: boolean = false;
  @Input() provider: Provider;
  providerGroups: ProviderGroup[] = [];
  categories: ProviderGroup[]=[];
  fees:Fee[]=[];
  selectedTextFee:number = null;
  selectedCallFee:number = null;
  selectedDegree:EducationalDegree = 3;
  getDegreeText = Provider.getDegreeString;
  constructor(
    private modal: ModalController,
    private modalController: ModalController,
    private env: EnvService,
    private providerService: ProviderService,
    private actionSheet:ActionSheetController,
    private alert :AlertService
  ) {}
  loadProviderCategories() {
    //this.env.somethingIsPending("fetching user provider groups");
    // return this.providerService.getCategories().pipe(
    //   map((result) => {
    //     //this.env.somethingIsDonePending("fetching user provider groups");
    //     return (this.providerGroups = result as ProviderGroup[]);
    //   })
    // );
  }
  loadFees()
  {
    return this.providerService.getFees().pipe(
      map((result) => {
        return (this.fees = result as Fee[]);
      })
    );
  }
  ngOnInit() {
    if(this.provider.provider_categories)
    {
          this.categories = this.provider.provider_categories;
    }
    this.selectedTextFee = (this.provider.per_minute_text_fee)?this.provider.per_minute_text_fee:null;
    this.selectedCallFee = (this.provider.per_minute_call_fee)?this.provider.per_minute_call_fee:null;
    //this.loadProviderCategories().subscribe();
    this.loadFees().subscribe();
  }
  addCategory() {
    if(!this.editable)
    return;
    this.presentSelectCategoryModal().subscribe();
  }
  presentSelectCategoryModal() {
    return from(
      this.modalController.create({
        component: SelectProviderCategoryComponent,
        componentProps: {
          providerGroups: this.providerGroups,
        },
      })
    ).pipe(
      concatMap((modalController) => {
        modalController.onDidDismiss;
        from(modalController.onDidDismiss()).subscribe((data) => {
          if (data["data"]) {
            if (data["data"]) {
              this.addCategoryToProvider(data["data"] as ProviderGroup);
            }
          }
        });
        return from(modalController.present());
      })
    );
  }
  addCategoryToProvider(selectedCategory: ProviderGroup) {
    let hasCat: any = false;
   
      hasCat = this.categories.find((category) => {
        return category.id == selectedCategory.id;
      });
   

    if (!hasCat) {
      this.categories.push(selectedCategory);
    }
  }
  deleteCategory(id) {
    if(!this.editable)
    return;
    let cat = this.categories.find((category) => {
      return category.id == id;
    });
    this.categories.splice(
      this.categories.indexOf(cat),
      1
    );
  }
  submit() {
    let provider = new Provider();
    provider.per_minute_call_fee = this.selectedCallFee;
    provider.per_minute_text_fee = this.selectedTextFee;
    provider.provider_categories = this.categories;
    provider.education_degree = this.selectedDegree;
    provider.id = this.provider.id;
    this.providerService.updateProviderInfo(provider).pipe(catchError(error=>{
      this.alert.presentError("به روزرسانی اطلاعات با مشکل روبرو شد.");
      return of(false);
    })).subscribe(result=>{
      if(result)
      {
        this.provider = result as Provider;
        this.alert.presentSuccess("به روزرسانی اطلاعات انجام شد.");
        this.dismiss();
      }
        
      })
  }
  dismiss(data = null) {
    this.modal.dismiss(data);
  }

  openCallFees()
  {
    if(!this.editable)
      return;
    let buttons= [];
    if(this.fees.length)
    {
      this.fees.filter(fee=>{
        return fee.name == "call";
      }).forEach(fee=>{
        buttons.push(
          {
            text: fee.value,
            role: "destructive",
            handler: () => {
              this.selectedCallFee = fee.value;
            },
          }
        )
      });
      this.alert.presentActionSheet(buttons,true).subscribe();

    }
  }
  isFormValid()
  {
    return this.selectedCallFee && this.selectedTextFee && this.categories.length;
  }
  openTextFees()
  {
    if(!this.editable)
    return;
    let buttons= [];
    if(this.fees.length)
    {
      this.fees.filter(fee=>{
        return fee.name == "text";
      }).forEach(fee=>{
        buttons.push(
          {
            text: fee.value,
            role: "destructive",
            handler: () => {
              this.selectedTextFee = fee.value;
            },
          }
        )
      });
      this.alert.presentActionSheet(buttons,true).subscribe();
    }
  }
  openDegrees()
  {
    if(!this.editable)
    return;
    let buttons= [];
    for(let i = 1; i< 5; i++)
    {
      buttons.push(
        {
          text: Provider.getDegreeString(i),
          role: "destructive",
          handler: () => {
            this.selectedDegree = i;
          },
        });
    }
      this.alert.presentActionSheet(buttons,true).subscribe();
    
  }
  
  
}
